import { action } from './helpers/actionHelpers';

export const REQUEST_PRODUCT_MEDIA_DATA = 'REQUEST_PRODUCT_MEDIA_DATA';

export const PRIMARY_PRODUCT_MEDIA_DATA_RECEIVED = 'PRIMARY_PRODUCT_MEDIA_DATA_RECEIVED';
export const PRIMARY_PRODUCT_MEDIA_DATA_UNAVAILABLE = 'PRIMARY_PRODUCT_MEDIA_DATA_UNAVAILABLE';

export const SECONDARY_PRODUCT_MEDIA_DATA_RECEIVED = 'SECONDARY_PRODUCT_MEDIA_DATA_RECEIVED';
export const SECONDARY_PRODUCT_MEDIA_DATA_UNAVAILABLE = 'SECONDARY_PRODUCT_MEDIA_DATA_UNAVAILABLE';

export const REQUEST_MULTIPLE_PRODUCT_MEDIA_DATA = 'REQUEST_MULTIPLE_PRODUCT_MEDIA_DATA';

export const requestProductMedia = (productCode, productName) => action(REQUEST_PRODUCT_MEDIA_DATA, { productCode, productName });

export const primaryProductMediaReceived = payload => action(PRIMARY_PRODUCT_MEDIA_DATA_RECEIVED, payload);
export const primaryProductMediaUnavailable = (productCode, productName) => action(
  PRIMARY_PRODUCT_MEDIA_DATA_UNAVAILABLE,
  { productCode, productName }
);

export const secondaryProductMediaReceived = payload => action(SECONDARY_PRODUCT_MEDIA_DATA_RECEIVED, payload);
export const secondaryProductMediaUnavailable = (productCode, productName) => action(
  SECONDARY_PRODUCT_MEDIA_DATA_UNAVAILABLE,
  { productCode, productName }
);
