import React from 'react';
import ReactDOM from 'react-dom';

const mountForgotPassword = (document) => {
  const mountPoint = document.getElementById('forgot-password');

  if (mountPoint) {
    return import(/* webpackChunkName: "ForgotPassword" */ '../../components/ForgotPassword/ForgotPasswordContainer')
      .then(({ default: ForgotPassword }) => {
        ReactDOM.render(<ForgotPassword />, mountPoint);
      });
  }

  return null;
};

export default mountForgotPassword;
