'use strict';

import '../scss/main.scss';
import { createStore } from 'redux';
import $ from 'jquery';
import navService from './components/facetnav';
import carouselService from './components/carousel';
import foldableService from './components/foldable';
import accordionService from './components/accordion';
import itemSwapService from './components/itemswap';
import pdpService from './components/pdp';
import lightboxService from './components/lightbox';
import tabsService from './components/tabs';
import loadmoreService from './components/loadMoreItems';
import hallettCartService from './components/hallettCheckout';
import paymentApp from './components/paymentDetails/reducers';
import configureCheckoutStore from './components/checkout/store/configureStore';
import metricsHelper from './components/metrics';
import passwordShowHide from './components/passwordInput';
import mountFitAsExpected from './mountPoints/fitAsExpected/mountFitAsExpected';
import mountCheckoutStoreLocator from './mountPoints/checkoutStoreLocator/mountCheckoutStoreLocator';
import mountCheckoutOrderSummary from './mountPoints/checkoutOrderSummary/mountCheckoutOrderSummary';
import mountPaymentDetails from './mountPoints/PaymentDetails/mountPaymentDetails';
import mountCheckoutDelivery from './mountPoints/CheckoutDelivery/mountCheckoutDelivery';
import mountCPMAccount from './mountPoints/CPMAccount/mountCPMAccount';
import mountCPMRegistration from './mountPoints/CPMRegistration/mountCPMRegistration';
import mountCPMEmailSignUp from './mountPoints/CPMEmailSignUp/mountCPMEmailSignUp';
import mountReCaptcha from './mountPoints/reCaptcha/mountReCaptcha';
import mountSizeGuide from './mountPoints/SizeGuide/mountSizeGuide';
import mountRecommendations from './mountPoints/Recommendations/mountRecommendations';
import mountMobileNav from './mountPoints/MobileNav/mountMobileNav';
import mountForgotPassword from './mountPoints/ForgotPassword/mountForgotPassword';
import mountSizeSelector from './mountPoints/SizeSelector/mountSizeSelector';
import mountProductImageCarousel from './mountPoints/ProductImageCarousel/mountProductImageCarousel';
import mountBrandSizeGuide from './mountPoints/BrandSizeGuide/mountBrandSizeGuide';

import configureStore from './mountPoints/configureStore';
import mountBrandLogo from './mountPoints/brandLogo/mountBrandLogo';
import {
  addHandlerToTealiumResetCookiePreferencesLink,
  handleTealiumResetCookiePreferencesClicked
} from './scriptlets/resetTealiumCookiePreferences';
import { extractDataAttributesFromElementId } from './helpers/domHelper/attributes';

const checkoutDelivery = document.getElementById('checkoutDelivery');
const paymentstore = createStore(paymentApp);
const userData = checkoutDelivery && checkoutDelivery.dataset ? { user: JSON.parse(JSON.stringify(checkoutDelivery.dataset)) } : {};
const checkoutstore = configureCheckoutStore(userData);
const lazyItems = document.getElementsByClassName('js-lazy-load');

// Initialize metricsHelper
metricsHelper.init();

// Capture any global settings
const globalSettings = extractDataAttributesFromElementId(document, 'js-global-settings');

// Initialise general redux store (not YET used by payments or checkout)
const store = configureStore({ globalSettings });

const getLazyLoad = () => import(/* webpackChunkName: "lazyload" */ 'vanilla-lazyload/dist/lazyload.min')
  .then(({ default: LazyLoad }) => new LazyLoad({
    elements_selector: '.js-lazy-load',
    class_loading: 'tu-c-lazy-load--loading',
    class_loaded: 'tu-c-lazy-load--loaded',
    class_error: 'tu-c-lazy-load--error'
  }));

if (lazyItems.length) getLazyLoad();

const onOverlayActive = () => {
  document.body.classList.add('has-overlay');
  document.documentElement.classList.add('has-overlay');
};

const onOverlayInactive = () => {
  document.body.classList.remove('has-overlay');
  document.documentElement.classList.remove('has-overlay');
};

/**
 * Mount React Components to DOM nodes
 */
mountPaymentDetails(paymentstore, document);
mountCheckoutDelivery(checkoutstore, document);
mountCPMAccount(document);
mountCPMRegistration(document);
mountFitAsExpected(store, document);
mountBrandLogo(document);
mountCheckoutStoreLocator(store, document, onOverlayActive, onOverlayInactive);
mountCheckoutOrderSummary(store, document, onOverlayActive, onOverlayInactive);
mountCPMEmailSignUp(document);
mountReCaptcha(document);
mountSizeGuide(document);
mountRecommendations(document);
mountMobileNav(document);
mountForgotPassword(document);
mountSizeSelector(document);
mountProductImageCarousel(store, document);
addHandlerToTealiumResetCookiePreferencesLink(document, handleTealiumResetCookiePreferencesClicked(window));
mountBrandSizeGuide(document);

// Remount product list components on infinite scroll
const remount = () => {
  mountBrandLogo(document);
  mountProductImageCarousel(store, document);
};

// Initialise legacy code
$(document).ready(() => {
  carouselService.init();
  navService.init();
  foldableService.addClickEvent();
  accordionService.addClickEvent();
  itemSwapService.init();
  pdpService.init();
  passwordShowHide.init();
  pdpService.$productVariantColour.on('change', (colorVariant) => {
    const variantURL = colorVariant.currentTarget.dataset.url || null;
    pdpService.loadColorVariant(variantURL);
  });
  pdpService.$productVariantSize.on('change', (sizeVariant) => {
    const variant = sizeVariant.currentTarget.value;
    const text = variant ? $(sizeVariant).find(`option[value='${variant}']`).text() : '';
    pdpService.setSize(variant, text);
  });
  pdpService.$qtyItem.on('change', (qtyitem) => {
    const variant = qtyitem.currentTarget.value || null;
    pdpService.changeQty(variant);
  });
  tabsService.addClickEvent();
  hallettCartService.init();
  $(document).on('click keypress touchend', `${lightboxService.$zoomClose}, ${lightboxService.$lightboxClick}`, () => {
    lightboxService.closeLightbox();
  });

  loadmoreService.init(remount);
});
