import { call, put, select } from 'redux-saga/effects';

import post from '../helpers/axiosPostHelper';
import config from '../config/orderSummary';
import { nectarCardApplied, applyNectarCardFailed } from '../actions/orderSummaryActions';

const globalSettingsSelector = state => state.globalSettings;

export function* applyNectarCard({ payload: { nectarCardNumber } }) {
  try {
    const globalSettings = yield select(globalSettingsSelector);
    yield call(
      post,
      config.nectarApiUrl,
      { nectarNumber: nectarCardNumber },
      { params: { 'CSRFToken': globalSettings.csrfToken } },
    );
    yield put(nectarCardApplied(nectarCardNumber));
  } catch (error) {
    yield put(applyNectarCardFailed(error));
  }
}
