import { failure } from './general';

export const REQUEST_STORES_LIST = 'REQUEST_STORES_LIST';
export const STORES_LIST_RECEIVED = 'STORES_LIST_RECEIVED';
export const REQUEST_NEAREST_POSTAL_CODE = 'REQUEST_NEAREST_POSTAL_CODE';
export const POSTCODE_OR_TOWN_RECEIVED = 'POSTCODE_OR_TOWN_RECEIVED';
export const POSTCODE_OR_TOWN_SUBMITTED = 'POSTCODE_OR_TOWN_SUBMITTED';
export const POSTCODE_OR_TOWN_LAST_ENTERED = 'POSTCODE_OR_TOWN_LAST_ENTERED';
export const POSTCODE_OR_TOWN_VALIDATED = 'POSTCODE_OR_TOWN_VALIDATED';
export const VALUE_MISSING_ERROR = 'VALUE_MISSING_ERROR';
export const PATTERN_MISMATCH_ERROR = 'PATTERN_MISMATCH_ERROR';
export const INCREMENT_CURRENT_PAGE = 'INCREMENT_CURRENT_PAGE';
export const PARTIALLY_SELECT_STORE = 'PARTIALLY_SELECT_STORE';
export const SELECT_STORE = 'SELECT_STORE';
export const SHOW_STORE_DETAILS = 'SHOW_STORE_DETAILS';
export const HIDE_STORE_DETAILS = 'HIDE_STORE_DETAILS';
export const REQUEST_GEOLOCATION = 'REQUEST_GEOLOCATION';
export const REQUEST_PREVIOUS_STORE = 'REQUEST_PREVIOUS_STORE';
export const PREVIOUS_STORE_RECEIVED = 'PREVIOUS_STORE_RECEIVED';
export const SELECT_PREVIOUS_STORE = 'SELECT_PREVIOUS_STORE';

export const storesListReceived = stores => ({
  type: STORES_LIST_RECEIVED,
  payload: { stores }
});

export const storesListFailed = error => failure(REQUEST_STORES_LIST, error);

export const requestNearestPostalCode = ({ apiKey, coords }) => ({
  type: REQUEST_NEAREST_POSTAL_CODE,
  payload: { apiKey, coords }
});

export const postcodeOrTownReceived = postcodeOrTown => ({
  type: POSTCODE_OR_TOWN_RECEIVED,
  payload: { postcodeOrTown }
});

export const postcodeOrTownSubmitted = postcodeOrTown => ({
  type: POSTCODE_OR_TOWN_SUBMITTED,
  payload: { postcodeOrTown }
});

export const postcodeOrTownLastEntered = postcodeOrTown => ({
  type: POSTCODE_OR_TOWN_LAST_ENTERED,
  payload: { postcodeOrTown }
});

export const postcodeOrTownValidated = postcodeOrTown => ({
  type: POSTCODE_OR_TOWN_VALIDATED,
  payload: { postcodeOrTown }
});

export const valueMissingError = () => failure(VALUE_MISSING_ERROR);

export const patternMismatchError = () => failure(PATTERN_MISMATCH_ERROR);

export const requestNearestPostalCodeFailed = error => failure(REQUEST_NEAREST_POSTAL_CODE, error);

export const incrementCurrentPage = () => ({
  type: INCREMENT_CURRENT_PAGE
});

export const partiallySelectStore = storeId => ({
  type: PARTIALLY_SELECT_STORE,
  payload: { storeId }
});

export const selectStore = storeId => ({
  type: SELECT_STORE,
  payload: { storeId }
});

export const showStoreDetails = storeId => ({
  type: SHOW_STORE_DETAILS,
  payload: { storeId }
});

export const hideStoreDetails = () => ({
  type: HIDE_STORE_DETAILS
});

export const requestGeolocationFailed = () => failure(REQUEST_GEOLOCATION);

export const requestPreviousStore = (storeId, postcodeOrTown) => ({
  type: REQUEST_PREVIOUS_STORE,
  payload: { storeId, postcodeOrTown }
});

export const previousStoreReceived = store => ({
  type: PREVIOUS_STORE_RECEIVED,
  payload: { store }
});

export const requestPreviousStoreFailed = error => failure(REQUEST_PREVIOUS_STORE, error);

export const selectPreviousStore = () => ({
  type: SELECT_PREVIOUS_STORE
});
