import { combineReducers } from 'redux';
import deliveries, * as fromDeliveries from './deliveries';
import user, * as fromUser from './user';
import fulfilmentConfig from '../config/fulfilment';

const paymentApp = combineReducers({
  deliveries,
  user
});

export default paymentApp;

// 'user' Selectors
export const getCartId = state => fromUser.getCartId(state.user);
export const getCSRFToken = state => fromUser.getCSRFToken(state.user);
export const getUserId = state => fromUser.getUserId(state.user);

// 'deliveries' Selectors
export const getSavedAddresses = state => fromDeliveries.getSavedAddresses(state.deliveries);
export const getSelectedSavedAddressId = (state, position = 0) => fromDeliveries
  .getSelectedSavedAddressId(state.deliveries, position);
export const getPostCodeOfSavedAddress = (state, savedAddressId) => fromDeliveries
  .getPostCodeOfSavedAddress(state.deliveries, savedAddressId);
export const getErrorStatus = state => fromDeliveries.getErrorStatus(state.deliveries);
export const getLoadingStatus = state => fromDeliveries.getLoadingStatus(state.deliveries);
export const getDeliveryItems = state => fromDeliveries.getDeliveryItems(state.deliveries);
export const getAddressEditDetails = state => fromDeliveries.getAddressEditDetails(state.deliveries);
export const getNewAddressAddedStatus = state => fromDeliveries.getNewAddressAddedStatus(state.deliveries);
export const getEditedAddressStatus = state => fromDeliveries.getEditedAddressStatus(state.deliveries);
export const getShowPanelSectionStatus = state => fromDeliveries.getShowPanelSectionStatus(state.deliveries);
export const getClickCollectAvailableStatus = state => fromDeliveries.getClickCollectAvailableStatus(state.deliveries);
export const getTotalPrice = state => fromDeliveries.getTotalPrice(state.deliveries);
export const getDeliveryFee = state => fromDeliveries.getDeliveryFee(state.deliveries);
export const getMultiDeliveryStatus = state => fromDeliveries.getMultiDeliveryStatus(state.deliveries);
export const getShowEditFormStatus = state => fromDeliveries.getShowEditFormStatus(state.deliveries);
export const getShowAddFormStatus = state => fromDeliveries.getShowAddFormStatus(state.deliveries);
export const getDeliveryType = (state, position = 0) => fromDeliveries
  .getDeliveryType(state.deliveries, position);
export const getFulfilmentCode = (state, position = 0) => fromDeliveries
  .getFulfilmentCode(state.deliveries, position);
export const getFulfilmentOptions = (state, position = 0) => fromDeliveries
  .getFulfilmentOptions(state.deliveries, position);
export const getFulfilmentDate = (state, position = 0) => fromDeliveries
  .getFulfilmentDate(state.deliveries, position);
export const getFulfilmentDates = (state, position = 0) => fromDeliveries
  .getFulfilmentDates(state.deliveries, position);
export const getProceedButtonStatus = (state, position = 0) => {
  const fulfilmentCode = getFulfilmentCode(state, position);
  const fulfilmentDate = getFulfilmentDate(state, position);
  if (!fulfilmentCode) { return false; }
  if (fulfilmentCode === fulfilmentConfig.premiumDelivery && !fulfilmentDate) {
    return false;
  }
  return true;
};
export const getNearestStores = state => fromDeliveries.getNearestStores(state.deliveries);
