import React from "react";
import Config from "../CPM/config/config";
import isError from "../../helpers/isError";
import postCPMEmailSignUp from "../../helpers/axiosPostHelper/index";
import Icon from "../paymentDetails/presentational/Icon";
import ErrorDisplay from "../ErrorDisplay/ErrorDisplay.component";
import errors from '../../config/errors';

class CPMEmailSignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: null,
      error: null,
      showSuccessBlock: "",
      submitted: "",
      submittedSuccess: false,
      errorMessage: ''
    };
  }

  handleSubmit = event => {
    event.preventDefault();

    // update state with value onchange
    this.setState({ email: event.target.value });
  };

  handleSubmitForm = e => {
    e.preventDefault();
    const { email } = this.state;

    // regex to test email validity
    const emailReg = errors.regexPatterns.email;
    const validEmailCheck = isError(email, emailReg);

    if (validEmailCheck) {
      const node = document.getElementById('js-cpm-email-sign-up');
      const csrfToken = node ? node.dataset.csrf : '';
      const url = `${Config.emailSignUp.cpmEmailPost}?CSRFToken=${csrfToken}&email=${email}`;

      // Clear error state
      this.setState({ error: false, errorMessage: '' });

      postCPMEmailSignUp(url)
        .then(response => {
          if (response && response.data) {
            this.setState({
              submittedSuccess: true,
              showSuccessBlock: "--success"
            });
          }
        })
        .catch(error => {
          this.setState({
            errorMessage: Config.emailSignUp.signUpFailure,
            error: true,
            showSuccessBlock: "--error"
          });
        });
    } else {
      this.setState({
        errorMessage: Config.emailSignUp.errorMessage,
        error: true,
        showSuccessBlock: "--error"
      });
    }
  };

  render() {
    const { submitted, submittedSuccess, showSuccessBlock, errorMessage, error } = this.state;

    return (
      <div
        data-testid='wrapper'
        className={`tu-c-cpm-email-sign-up${showSuccessBlock} ln-u-push-bottom ln-u-push-top`}
      >
        <form action="" noValidate="novalidate" onSubmit={e => this.handleSubmitForm(e)}>
          <label
            htmlFor="email-sign-up-input"
            className="ln-c-label ln-u-h4 is-required tu-c-cpm-email-sign-up__label ln-u-text-align-center"
          >
            {Config.emailSignUp.headerLabel}
          </label>
          <div className="tu-c-cpm-email-sign-up__error-input-wrapper ln-u-text-align-center ln-u-display-inline-block">
            <div className="tu-c-cpm-email-sign-up__email-input-wrapper">
              <div
                className={`ln-c-form-group ${
                  error ? "has-error" : ""
                  } tu-c-cpm-email-sign-up__button ln-u-display-flex`}
              >
                <span className="ln-c-input-container tu-c-cpm-email-sign-up__email-error-container">
                  <input
                    data-testid='email-input'
                    name="email"
                    type="email"
                    id="email-sign-up-input"
                    onChange={event => this.handleSubmit(event)}
                    placeholder={Config.emailSignUp.placeholder}
                    className="ln-c-text-input tu-c-cpm-email-sign-up__tu-u-form-input-sign-up"
                  />
                  <a
                    href="#"
                    data-testid='submit-button'
                    className={`tu-c-cpm-email-sign-up__tu-u-submit-button ${submitted} ${
                      submittedSuccess
                        ? "tu-c-cpm-email-sign-up__tu-u-form-submitted"
                        : ""
                      }`}
                    onClick={e => this.handleSubmitForm(e)}
                  >
                    <div className="tu-c-cpm-email-sign-up__tu-u-button-wrapper ln-u-text-align-right">
                      <span className="tu-c-cpm-email-sign-up__tu-u-sign-up ln-u-font-weight-bold tu-text-uppercase">
                        {Config.emailSignUp.signUp}
                      </span>
                      <Icon
                        classes="ln-c-icon ln-c-icon--large"
                        viewBox="0 0 17 17"
                        icon="link-arrow"
                        stroke="transparent"
                      />
                    </div>
                  </a>
                </span>
              </div>
              <div className="tu-c-cpm-email-sign-up__tu-u-email-confirm ln-u-display-flex">
                <span className="tu-c-cpm-email-sign-up__tu-u-error-wrapper tu-u-text-uppercase ln-u-display-flex">
                  {Config.emailSignUp.signUpConfirmation}</span>
              </div>
            </div>
          </div>
          {error && (
            <ErrorDisplay
              classes={`ln-u-push-top ln-u-text-align-left tu-c-cpm-email-sign-up__email-input-error`}
              error={errorMessage}
            />
          )}
        </form>
      </div>
    );
  }
}

export default CPMEmailSignUp;
