import axios from 'axios';

/*
  axios get function;

  function returns a promise to be handled inside parent component

  *************************** USAGE ******************************
  * import helper and pass url and params (this will be an object
    containing data) to be sent
* */

const requestedWithXhrHeaders = { 'X-Requested-With': 'XMLHttpRequest' };

const requestConfig = (params, headers) => {
  const config = {};

  if (params) {
    config.params = params;
  }

  if (headers) {
    config.headers = headers;
  }

  return config;
};
const axiosGetHelper = (url, params, headers) => axios.get(url, requestConfig(params, headers));

const xhrRequestConfig = (params, headers) => requestConfig(params, { ...requestedWithXhrHeaders, ...headers });
const xhrGet = (url, params, headers) => axios.get(url, xhrRequestConfig(params, headers));

export {
  axiosGetHelper as default,
  xhrGet,
  requestConfig,
  xhrRequestConfig
};
