import $ from 'jquery';
import axios from 'axios';

const lightboxService = (function () {
  return {
    $lightboxInit: false,
    $x: 0,
    $y: 0,
    $prev_x: 0,
    $prev_y: 0,
    $obj: false,
    $lightBoxOpen: 'tu-lightbox__container--open',
    $lightboxContainer: 'tu-lightbox__container',
    $lightboxClick: '#tu-lightbox__click',
    $lightboxShow: 'tu-lightbox__container--show',
    $imageZoom: $('.js-img-zoomable'),
    $htmlZoom: $('.js-html-zoomable'),
    $zoomClose: '.js-lightbox-close',
    $draggable: '#draggable',
    $draggableArea: '#draggable-area',
    $draggableAreaDisabled: 'tu-lightbox__image-drag-area--disabled',
    $paddingPercent: 1.35,
    $lightboxTarget: $('body'),
    $lightboxButton: `<button type='button' class='tu-lightbox__btn js-lightbox-close'>
                        <svg role='presentation' version='1.1' viewBox='-17 -17 66 66' class='ln-c-icon ln-c-icon--large'>
                          <use xmlns:xlink='http://www.w3.org/1999/xlink' xlink:href='/_ui/2017/symbol/svg/sprite.symbol.svg#cross' fill='#ffffff'></use>
                        </svg>
                      </button>`,
    _setContainerInit: function (init = false) {
      this.$lightboxInit = init;
    },
    _imgContent: function (image, alt, height) {
      const setImage = `<div class='tu-lightbox__image'>
                          ${this.$lightboxButton}
                          <div class='tu-lightbox__image-drag' style='height:${height}px'>
                            <img src='${image}' alt='${alt}' id='draggable' />
                            <div class="tu-c-spinner tu-c-spinner--active">
                              <div class="tu-c-spinner__inner"></div>
                            </div>
                            <div class='tu-lightbox__image-drag-area tu-lightbox__image-drag-area--disabled' id='draggable-area'></div>
                          </div>
                        </div>`;
      return setImage;
    },
    _htmlContent: function (html) {
      const setHtml = `<div class='tu-lightbox__html'>
                          ${this.$lightboxButton}
                          ${html}
                        </div>`;
      return setHtml;
    },
    _lightboxContent: function (content) {
      const lightbox = `<div class='tu-lightbox__click' id='tu-lightbox__click'></div>
                        <div class='tu-lightbox__content'>
                          ${content}
                        </div>`;
      return lightbox;
    },
    _getHeight: function () {
      return $(window).height();
    },
    createContainer: function () {
      const container = document.createElement('div');
      container.className = this.$lightboxContainer;
      container.id = this.$lightboxContainer;
      this.$lightboxTarget.prepend(container);
    },
    getLightbox: function (type = 'img', content = { img: null, alt: null, html: null }, position = {}) {
      const windowHeight = this._getHeight() - 80;
      let setContent;

      if (type === 'img') {
        setContent = this._imgContent(content.img, content.alt, windowHeight);
      } else {
        setContent = this._htmlContent(content.html);
      }

      const lightBox = this._lightboxContent(setContent);

      if (!this.$lightboxInit) {
        this.createContainer();
      }

      if (type === 'img') {
        $(`#${this.$lightboxContainer}`).empty().html(lightBox).addClass(this.$lightboxShow);
        const setImgDrag = $(`#${this.$lightboxContainer}`).find('.tu-lightbox__image-drag');
        const setImg = setImgDrag.find('img');
        let percentPosition;
        let w;
        let h;
        let dw;
        let dh;
        let setLeft;
        let setTop;
        let axis = {};

        setImg.on('load', () => {
          percentPosition = lightboxService.getClickPosition(position);
          w = lightboxService.findWidth(setImg);
          h = lightboxService.findHeight(setImg);
          dw = lightboxService.findWidth(setImgDrag);
          dh = windowHeight;
          setLeft = lightboxService.findPoint(percentPosition.x, w, dw);
          setTop = lightboxService.findPoint(percentPosition.y, h, dh);
          axis = {
            w: w,
            dw: dw,
            h: h,
            dh: dh
          };
          setImg.css({ 'left': `-${setLeft}px`, 'top': `-${setTop}px` });
          setImgDrag.find('.tu-c-spinner').removeClass('tu-c-spinner--active');
          // eslint-disable-next-line max-len
          setImgDrag.find(lightboxService.$draggableArea).removeClass(lightboxService.$draggableAreaDisabled);
          lightboxService.draggable(
            $(lightboxService.$draggable),
            $(lightboxService.$draggableArea),
            axis
          );
        });
      }

      if (type === 'html') {
        $(`#${this.$lightboxContainer}`).empty().html(lightBox).addClass(this.$lightboxShow);
      }
      if (!this.$lightboxInit) {
        this._setContainerInit(true);
      }
    },
    closeLightbox: function () {
      $(`#${this.$lightboxContainer}`).removeClass(this.$lightboxShow);
    },
    attachZoomHandler: function () {
      $(this.$imageZoom).on('click keypress', function (event) {
        const current = $(this);
        const position = {
          x: event.offsetX,
          y: event.offsetY,
          w: current.width(),
          h: current.height()
        };
        const image = {
          img: current.data('zoom'),
          alt: current.attr('alt'),
          html: ''
        };
        lightboxService.getLightbox('img', image, position);
      });

      $(this.$htmlZoom).on('click keypress', function (event) {
        event.preventDefault();
        const current = $(this);
        axios({
          method: 'get',
          url: current.attr('href')
        })
          .then((response) => {
            lightboxService.getLightbox('html', { html: response.data }, { });
          })
          .catch((error) => {
            console.log(error);
          });
      });
    },
    findWidth: function (img) {
      return img.width();
    },
    findHeight: function (img) {
      return img.height();
    },
    findPaddedHeight: function (base) {
      return base * this.$paddingPercent;
    },
    findPercentage: function (fraction, base) {
      return fraction / base;
    },
    findPoint: function (percent, item, container) {
      return Math.round((item - container) * percent);
    },
    getClickPosition: function (position) {
      const xPosition = this.findPercentage(position.x, position.w);
      const yPosition = this.findPercentage(position.y, position.h);
      return { x: xPosition, y: yPosition };
    },
    draggable: function (element, dragArea, axis) {
      const item = element;
      const area = dragArea;
      const maxWidth = axis.dw - axis.w;
      const maxHeight = axis.dh - axis.h;
      let dragging = null;
      let allowDragging = false;

      // eslint-disable-next-line prefer-arrow-callback
      area.on('mousedown touchstart', function (event) {
        const e = window.event || event;
        let xClient = e.clientX;
        let yClient = e.clientY;
        if (event.type != 'mousedown') {
          xClient = e.targetTouches[0].pageX;
          yClient = e.targetTouches[0].pageY;
        }

        dragging = {
          mouseX: xClient,
          mouseY: yClient,
          startX: parseInt(item[0].style.left, 10),
          startY: parseInt(item[0].style.top, 10)
        };
        allowDragging = true;
      });

      $(document).on('mouseup touchend', () => {
        allowDragging = false;
        dragging = null;
      });
      // eslint-disable-next-line prefer-arrow-callback
      area.on('mousemove touchmove', function (event) {
        if (allowDragging) {
          const e = window.event || event;
          let xClientX = e.clientX;
          let yClientY = e.clientY;
          if (event.type != 'mousemove') {
            xClientX = e.targetTouches[0].pageX;
            yClientY = e.targetTouches[0].pageY;
          }

          const top = dragging.startY + (yClientY - dragging.mouseY);
          const left = dragging.startX + (xClientX - dragging.mouseX);

          if (top <= 0 && top > maxHeight) {
            item[0].style.top = `${top}px`;
          }
          if (left <= 0 && left > maxWidth) {
            item[0].style.left = `${left}px`;
          }
        }
      });
    }
  };
}());

export default lightboxService;
