// Actions with general applicability (should be FEW and far between)
import { action } from './helpers/actionHelpers';

export const ERROR = 'ERROR';
export const FAILURE = 'FAILURE';
export const NEW_RELIC_PAGE_ACTION_SUBMITTED = 'NEW_RELIC_PAGE_ACTION_SUBMITTED';

export const failure = (source, error) => ({
  type: FAILURE,
  payload: { source, error }
});

export const error = err => ({ type: ERROR, payload: err });

export const newRelicPageActionSubmitted = (eventName, eventData) => action(NEW_RELIC_PAGE_ACTION_SUBMITTED, {
  eventName,
  eventData
});
