import $ from 'jquery';

const foldableService = (function () {
  return {
    foldablePanelToggle: $('.js-foldable-toggle'),
    foldableToggle: '.js-foldable-toggle',
    foldablePanel: '.js-foldable',
    foldablePanelOpen: 'is-open',
    addClickEvent: function () {
      $(document).on('click', this.foldableToggle, function () {
        const $current = $(this);
        $current.closest(foldableService.foldablePanel)
          .toggleClass(foldableService.foldablePanelOpen);
      });
    }
  };
}());

export default foldableService;
