import {
  STORES_LIST_RECEIVED, REQUEST_STORES_LIST, POSTCODE_OR_TOWN_RECEIVED,
  POSTCODE_OR_TOWN_SUBMITTED, POSTCODE_OR_TOWN_LAST_ENTERED, REQUEST_NEAREST_POSTAL_CODE,
  INCREMENT_CURRENT_PAGE, PARTIALLY_SELECT_STORE, SELECT_STORE, SHOW_STORE_DETAILS, HIDE_STORE_DETAILS,
  REQUEST_GEOLOCATION, PREVIOUS_STORE_RECEIVED, REQUEST_PREVIOUS_STORE, SELECT_PREVIOUS_STORE,
  POSTCODE_OR_TOWN_VALIDATED, PATTERN_MISMATCH_ERROR, VALUE_MISSING_ERROR
} from '../actions/storeLocatorActions';
import { FAILURE } from '../actions/general';
import { formatOpeningTimes, formatSpecialOpeningTimes } from './openingTimes';

const nullifyIfEmpty = str => str || null;
const isNotNull = str => !!str;

const buildAddress = (address) => {
  if (!address) {
    return null;
  }

  return [
    address.address1,
    nullifyIfEmpty(address.address2),
    address.city,
    isNotNull(address.post_code) ? address.post_code.toUpperCase() : null
  ]
    .filter(isNotNull)
    .join(', ');
};

const buildDistance = (distance) => {
  if (distance && distance.value && distance.units) {
    return `${distance.value.toFixed(1)} ${distance.units}`;
  }
  return null;
};

const buildLocation = location => nullifyIfEmpty(location);

const buildCollection = collection => (collection
  ? `Your order will be ready to collect ${collection} days.`
  : null);

const toDomainStore = (store) => {
  const location = buildLocation(store.location);
  return ({
    id: store.id,
    name: store.name,
    address: buildAddress(store.address),
    distance: buildDistance(store.distance),
    collection: buildCollection(nullifyIfEmpty(store.clickAndCollectDescription)),
    location,
    telephone: store.contact ? store.contact.telephone : null,
    openingTimes: formatOpeningTimes(store.opening_times),
    specialOpeningTimes: formatSpecialOpeningTimes(store.exception_times),
    showMapPin: location !== null
  });
};

const PAGE_SIZE = 3;

const MAX_STORES_SEARCH_ATTEMPTS = 3;

const defaultState = {
  stores: null,
  paginatedStores: null,
  storePlaceholdersCount: PAGE_SIZE,
  isLoadingStores: false,
  previousStore: null,
  isLoadingPreviousStore: false,
  isPreviousStoreSelected: false,
  storesSearchAttempts: 0,
  postcodeOrTown: '',
  currentPage: 1,
  showMoreResults: false,
  partiallySelectedStoreId: null,
  selectedStoreId: null,
  storeDetails: null,
  error: null,
  lookupError: null
};

const getStoresUpToPage = (stores, page) => stores.slice(0, PAGE_SIZE * page);

const isShowMoreResultsEnabled = (paginatedStores, stores) => paginatedStores.length < stores.length;

const calculateStoresPlaceholdersCount = stores => (stores && stores.length) || defaultState.storePlaceholdersCount;

const storesListReceived = (state, action) => {
  const stores = action.payload.stores.map(toDomainStore);
  const paginatedStores = getStoresUpToPage(stores, defaultState.currentPage);
  const showMoreResults = isShowMoreResultsEnabled(paginatedStores, stores);

  return {
    ...state,
    stores,
    paginatedStores,
    storePlaceholdersCount: calculateStoresPlaceholdersCount(paginatedStores),
    isLoadingStores: defaultState.isLoadingStores,
    storesSearchAttempts: defaultState.storesSearchAttempts,
    currentPage: defaultState.currentPage,
    showMoreResults,
    partiallySelectedStoreId: defaultState.partiallySelectedStoreId,
    selectedStoreId: defaultState.selectedStoreId,
    storeDetails: defaultState.storeDetails,
    error: defaultState.error
  };
};

const persistPostcodeOrTown = (state, action) => ({
  ...state,
  postcodeOrTown: action.payload.postcodeOrTown
});

const incrementCurrentPage = (state) => {
  const { currentPage, stores } = state;

  const nextPage = currentPage + 1;
  const paginatedStores = getStoresUpToPage(stores, nextPage);
  const showMoreResults = isShowMoreResultsEnabled(paginatedStores, stores);

  return {
    ...state,
    currentPage: nextPage,
    paginatedStores,
    storePlaceholdersCount: calculateStoresPlaceholdersCount(paginatedStores),
    showMoreResults
  };
};

const enableIsLoadingStores = state => ({ ...state, isLoadingStores: true });

const disableIsLoadingStores = state => ({ ...state, isLoadingStores: defaultState.isLoadingStores });

const enableIsLoadingPreviousStore = state => ({ ...state, isLoadingPreviousStore: true });

const disableIsLoadingPreviousStore = state => ({ ...state, isLoadingPreviousStore: defaultState.isLoadingPreviousStore });

const selectPreviousStore = state => ({ ...state, isPreviousStoreSelected: true });

const unselectPreviousStore = state => ({ ...state, isPreviousStoreSelected: defaultState.isPreviousStoreSelected });

const disableShowMoreResults = state => ({ ...state, showMoreResults: false });

const incrementStoresSearchAttempts = state => ({
  ...state,
  storesSearchAttempts: state.storesSearchAttempts + 1
});

const partiallySelectStore = (state, action) => ({
  ...state,
  partiallySelectedStoreId: action.payload.storeId
});

const selectStore = (state, action) => ({
  ...state,
  selectedStoreId: action.payload.storeId
});

const resetLookupState = state => ({
  ...state,
  stores: defaultState.stores,
  paginatedStores: defaultState.paginatedStores,
  isLoadingStores: defaultState.isLoadingStores,
  storesSearchAttempts: defaultState.storesSearchAttempts,
  postcodeOrTown: defaultState.postcodeOrTown,
  currentPage: defaultState.currentPage,
  showMoreResults: defaultState.showMoreResults,
  partiallySelectedStoreId: defaultState.partiallySelectedStoreId,
  selectedStoreId: defaultState.selectedStoreId,
  error: defaultState.error,
  lookupError: defaultState.lookupError
});

const resetLookupError = state => ({
  ...state,
  lookupError: defaultState.lookupError
});

const initiateStoresSearch = state => disableShowMoreResults(enableIsLoadingStores(resetLookupError(state)));

const buildStoreDetails = (paginatedStores, previousStore, storeDetailsId) => {
  if (!paginatedStores && !previousStore) {
    return null;
  }
  if (previousStore && previousStore.id === storeDetailsId) {
    return previousStore;
  }
  return nullifyIfEmpty(paginatedStores.find(store => store.id === storeDetailsId));
};

const showStoreDetails = (state, action) => ({
  ...state,
  storeDetails: buildStoreDetails(
    state.paginatedStores,
    state.previousStore,
    action.payload.storeId
  )
});

const hideStoreDetails = state => ({
  ...state,
  storeDetails: null
});

const previousStoreReceived = (state, action) => ({
  ...state,
  previousStore: toDomainStore(action.payload.store),
  isLoadingPreviousStore: defaultState.isLoadingPreviousStore
});

const resetState = state => ({
  ...state,
  stores: defaultState.stores,
  paginatedStores: defaultState.paginatedStores,
  currentPage: defaultState.currentPage,
  showMoreResults: defaultState.showMoreResults,
  partiallySelectedStoreId: defaultState.partiallySelectedStoreId,
  selectedStoreId: defaultState.selectedStoreId,
  storeDetails: defaultState.storeDetails,
  isLoadingStores: defaultState.isLoadingStores
});

const hasReachedMaximumSearchAttempts = state => state.storesSearchAttempts === MAX_STORES_SEARCH_ATTEMPTS;

const addError = state => ({
  ...state,
  error: 'Unable to find stores near your location'
});

const addUnrecoverableError = state => ({
  ...state,
  error: {
    header: 'This store location service is currently down',
    body:
      'Please use the delivery selector instead as we are having issues with the store selector service.'
  }
});

const addPatternMismatchError = state => ({
  ...state,
  lookupError: 'Please enter a valid location'
});

const addValueMissingError = state => ({
  ...state,
  lookupError: 'Please enter a location'
});

const addGeolocationError = state => ({
  ...state,
  lookupError: 'Please enable location access or enter a postcode or town'
});

const failure = (state, action) => {
  const nextState = resetState(state);

  switch (action.payload.source) {
    case REQUEST_GEOLOCATION:
      return addGeolocationError(disableIsLoadingStores(state));
    case REQUEST_PREVIOUS_STORE:
      return disableIsLoadingPreviousStore(state);
    case REQUEST_NEAREST_POSTAL_CODE:
      return addError(nextState);
    case PATTERN_MISMATCH_ERROR:
      return addPatternMismatchError(state);
    case VALUE_MISSING_ERROR:
      return addValueMissingError(state);
    case REQUEST_STORES_LIST:
    default:
      return hasReachedMaximumSearchAttempts(state)
        ? addUnrecoverableError(nextState)
        : addError(incrementStoresSearchAttempts(nextState));
  }
};

const storeLocatorReducer = (
  state = defaultState,
  action = { type: '', payload: {} }
) => {
  switch (action.type) {
    case STORES_LIST_RECEIVED:
      return storesListReceived(state, action);
    case POSTCODE_OR_TOWN_RECEIVED:
    case POSTCODE_OR_TOWN_SUBMITTED:
      return persistPostcodeOrTown(state, action);
    case POSTCODE_OR_TOWN_LAST_ENTERED:
      return initiateStoresSearch(persistPostcodeOrTown(state, action));
    case POSTCODE_OR_TOWN_VALIDATED:
      return initiateStoresSearch(state);
    case INCREMENT_CURRENT_PAGE:
      return incrementCurrentPage(state);
    case PARTIALLY_SELECT_STORE:
      return partiallySelectStore(state, action);
    case SELECT_STORE:
      return selectStore(unselectPreviousStore(state), action);
    case SELECT_PREVIOUS_STORE:
      return selectPreviousStore(resetLookupState(state));
    case SHOW_STORE_DETAILS:
      return showStoreDetails(state, action);
    case HIDE_STORE_DETAILS:
      return hideStoreDetails(state);
    case REQUEST_PREVIOUS_STORE:
      return enableIsLoadingPreviousStore(state);
    case PREVIOUS_STORE_RECEIVED:
      return previousStoreReceived(state, action);
    case FAILURE:
      return failure(state, action);
    default:
      return state;
  }
};

export default storeLocatorReducer;
