const COLLECTION = 'CLICK_AND_COLLECT';
const HOME_DELIVERY = 'HOME';
const VALID_TYPES = [COLLECTION, HOME_DELIVERY];

const isInvalidType = type => VALID_TYPES.includes(type) === false;

const isPremiumDelivery = code => code === 'premium-delivery';

const buildType = isCollection => (isCollection ? 'collection' : 'delivery');

const buildCustomerName = (firstName, lastName) => `${firstName} ${lastName}`;

const buildCost = cost => (cost === '0.00' ? 'FREE' : cost);

const nullifyIfEmpty = str => str || null;

const isNotNull = str => !!str;

const buildPremiumDeliveryMethod = (deliveryInstructions, deliveryDate) => [
  nullifyIfEmpty(deliveryInstructions),
  `(${deliveryDate})`
].filter(isNotNull)
  .join(' ');

const buildAddress = ({
  houseNumber, line1, line2, townCity
}) => [
  nullifyIfEmpty(houseNumber),
  line1,
  nullifyIfEmpty(line2),
  townCity.toUpperCase()
].filter(isNotNull)
  .join(', ');

const toDomainFulfilment = (fulfilmentOption, deliveryAddress) => {
  if (!fulfilmentOption || !deliveryAddress) {
    return null;
  }

  if (isInvalidType(fulfilmentOption.type)) {
    return null;
  }

  const {
    code, type, storeName, deliveryCost, deliveryInstructions, namedDeliveryDateFormatted
  } = fulfilmentOption;
  const {
    firstName, lastName, postCode, houseNumber, line1, line2, townCity
  } = deliveryAddress;

  const isCollection = type === COLLECTION;

  return {
    type: buildType(isCollection),
    name: isCollection ? storeName : buildCustomerName(firstName, lastName),
    cost: buildCost(deliveryCost),
    postcode: postCode.toUpperCase(),
    address: buildAddress({
      houseNumber, line1, line2, townCity
    }),
    method: isPremiumDelivery(code)
      ? buildPremiumDeliveryMethod(deliveryInstructions, namedDeliveryDateFormatted)
      : nullifyIfEmpty(deliveryInstructions)
  };
};

export { toDomainFulfilment };
