import { call, put } from 'redux-saga/effects';

import { error } from '../../actions/general';

function* restartFailedSaga(saga, ...args) {
  while (true) {
    try {
      yield call(saga, ...args);
      break;
    } catch (err) {
      yield put(error(err));
    }
  }
}

export default restartFailedSaga;
