import React from 'react';
import ReactDOM from 'react-dom';

const mountCheckoutDelivery = (store, document) => {
  const mountPoint = document.getElementById('checkoutDelivery');

  if (mountPoint) {
    return import(/* webpackChunkName: "CheckoutDelivery" */ './CheckoutDelivery.provider')
      .then(({ default: ProvidedCheckoutDelivery }) => {
        ReactDOM.render(<ProvidedCheckoutDelivery store={store} />, mountPoint);
      });
  }

  return null;
};

export default mountCheckoutDelivery;
