import {
  PRIMARY_PRODUCT_MEDIA_DATA_RECEIVED,
  SECONDARY_PRODUCT_MEDIA_DATA_RECEIVED,
  SECONDARY_PRODUCT_MEDIA_DATA_UNAVAILABLE
} from '../actions/productMediaActions';
import {
  buildPrimaryImageUrls,
  buildSecondaryImageUrls,
  buildFallbackImageUrls
} from './helpers/carouselUrlBuilder';
import { fallbackImageUrl } from './config/defaults.json';

const productMediaReducer = (state = {}, { type = '', payload = {} } = {}) => {
  switch (type) {
    case PRIMARY_PRODUCT_MEDIA_DATA_RECEIVED:
      return {
        ...state,
        [payload.productCode]: buildPrimaryImageUrls(
          payload.attributes.images,
          payload.productName,
          { pageType: payload.pageType }
        )
      };

    case SECONDARY_PRODUCT_MEDIA_DATA_RECEIVED:
      return {
        ...state,
        [payload.productCode]: buildSecondaryImageUrls(
          payload.imageResource.cdnImages,
          payload.productName
        )
      };

    case SECONDARY_PRODUCT_MEDIA_DATA_UNAVAILABLE:
      return {
        ...state,
        [payload.productCode]: buildFallbackImageUrls(
          fallbackImageUrl,
          payload.productName
        )
      };

    default:
      return state;
  }
};

export default productMediaReducer;
