import React from 'react';
import ReactDOM from 'react-dom';

const mountReCaptcha = (document) => {
  const mountPointLogin = document.getElementById('reCaptcha-login');
  const mountPointRegister = document.getElementById('reCaptcha-register');

  return import(/* webpackChunkName: "ReCaptcha" */ '../../components/reCaptcha/loadreCaptcha')
    .then(({ default: ReCaptcha }) => {
      if (mountPointLogin) {
        ReactDOM.render(<ReCaptcha formName='loginForm' buttonName='submit-login' />, mountPointLogin);
      }

      if (mountPointRegister) {
        ReactDOM.render(<ReCaptcha formName='registerForm' buttonName='submit-register' />, mountPointRegister);
      }
    });
};

export default mountReCaptcha;
