import React from 'react';
import PropTypes from 'prop-types';

const UtilityLink = ({ item }) => {
  const { text, url } = item;

  return text
    ? (
      <a
        href={url}
        title={text}
        className='ln-u-display-block ln-u-text-decoration-normal'
      >
        <div className='ln-u-display-flex ln-u-align-items-center ln-u-push-top
        tu-c-mobile-nav__list-container--item'
        >
          <div className='ln-u-soft-left ln-u-font-weight-regular'>{text}</div>
        </div>
      </a>
    )
    : null;
};

UtilityLink.propTypes = {
  item: PropTypes.exact({
    id: PropTypes.string,
    type: PropTypes.oneOf(['utility']),
    text: PropTypes.string,
    url: PropTypes.string
  }).isRequired
};

export default UtilityLink;
