import {
  all, spawn, takeLatest, takeEvery
} from 'redux-saga/effects';

import restartFailedSaga from './helpers/restartFailedSaga';
import actionHandlerSaga from './helpers/actionHandlerSaga';

import { REQUEST_BAZAAR_VOICE_DATA } from '../actions/bazaarVoiceActions';
import {
  REQUEST_NEAREST_POSTAL_CODE, POSTCODE_OR_TOWN_RECEIVED, POSTCODE_OR_TOWN_SUBMITTED,
  POSTCODE_OR_TOWN_VALIDATED, REQUEST_PREVIOUS_STORE
} from '../actions/storeLocatorActions';
import {
  REQUEST_PRODUCT_MEDIA_DATA,
  PRIMARY_PRODUCT_MEDIA_DATA_UNAVAILABLE,
} from '../actions/productMediaActions';
import { NEW_RELIC_PAGE_ACTION_SUBMITTED } from '../actions/general';

import { getBazaarVoiceApiData } from './bazaarVoiceApiSaga';
import { fetchStoresList } from './storeLocatorSaga';
import { fetchPrimaryProductMediaData } from './primaryProductMediaSaga';
import { fetchSecondaryProductMediaData } from './secondaryProductMediaSaga';
import { fetchNearestPostalCode } from './nearestPostalCodeSaga';
import { fetchPreviousStore } from './previousStoreSaga';
import { validatePostcodeOrTown } from './validatePostcodeOrTownSaga';
import { submitPageActionToNewRelic } from './submitPageActionToNewRelicSaga';
import {
  REQUEST_ORDER_SUMMARY, NECTAR_CARD_SUBMITTED, NECTAR_CARD_VALIDATED
} from '../actions/orderSummaryActions';
import { fetchOrderSummary } from './orderSummarySaga';
import { validateNectarCard } from './validateNectarCardSaga';
import { applyNectarCard } from './applyNectarCardSaga';

function* rootSaga() {
  const actionHandlerConfigs = [
    [takeLatest, REQUEST_BAZAAR_VOICE_DATA, getBazaarVoiceApiData],
    [takeEvery, REQUEST_PRODUCT_MEDIA_DATA, fetchPrimaryProductMediaData],
    [takeEvery, PRIMARY_PRODUCT_MEDIA_DATA_UNAVAILABLE, fetchSecondaryProductMediaData],
    [takeEvery, REQUEST_NEAREST_POSTAL_CODE, fetchNearestPostalCode],
    [takeEvery, POSTCODE_OR_TOWN_RECEIVED, fetchStoresList],
    [takeEvery, POSTCODE_OR_TOWN_SUBMITTED, validatePostcodeOrTown],
    [takeEvery, POSTCODE_OR_TOWN_VALIDATED, fetchStoresList],
    [takeEvery, REQUEST_PREVIOUS_STORE, fetchPreviousStore],
    [takeEvery, REQUEST_ORDER_SUMMARY, fetchOrderSummary],
    [takeEvery, NECTAR_CARD_SUBMITTED, validateNectarCard],
    [takeEvery, NECTAR_CARD_VALIDATED, applyNectarCard],
    [takeEvery, NEW_RELIC_PAGE_ACTION_SUBMITTED, submitPageActionToNewRelic]
  ];

  yield all(
    actionHandlerConfigs.map(
      handlerConfig => spawn(
        restartFailedSaga,
        actionHandlerSaga,
        handlerConfig
      )
    )
  );
}

export default rootSaga;
