import { failure } from './general';

export const REQUEST_ORDER_SUMMARY = 'REQUEST_ORDER_SUMMARY';
export const ORDER_SUMMARY_RECEIVED = 'ORDER_SUMMARY_RECEIVED';
export const TOGGLE_NECTAR_FORM = 'TOGGLE_NECTAR_FORM';
export const APPLY_NECTAR_CARD = 'APPLY_NECTAR_CARD';
export const NECTAR_CARD_SUBMITTED = 'NECTAR_CARD_SUBMITTED';
export const NECTAR_CARD_VALIDATED = 'NECTAR_CARD_VALIDATED';
export const INVALID_NECTAR_CARD_ERROR = 'INVALID_NECTAR_CARD_ERROR';
export const NECTAR_CARD_APPLIED = 'NECTAR_CARD_APPLIED';
export const SHOW_CART_DETAILS = 'SHOW_CART_DETAILS';
export const HIDE_CART_DETAILS = 'HIDE_CART_DETAILS';

export const requestOrderSummary = () => ({
  type: REQUEST_ORDER_SUMMARY
});

export const orderSummaryReceived = orderSummary => ({
  type: ORDER_SUMMARY_RECEIVED,
  payload: { orderSummary }
});

export const requestOrderSummaryFailed = error => failure(REQUEST_ORDER_SUMMARY, error);

export const toggleNectarForm = () => ({
  type: TOGGLE_NECTAR_FORM
});

export const applyNectarCard = nectarCardNumber => ({
  type: APPLY_NECTAR_CARD,
  payload: { nectarCardNumber }
});

export const nectarCardSubmitted = nectarCardNumber => ({
  type: NECTAR_CARD_SUBMITTED,
  payload: { nectarCardNumber }
});

export const nectarCardValidated = nectarCardNumber => ({
  type: NECTAR_CARD_VALIDATED,
  payload: { nectarCardNumber }
});

export const nectarCardValidationError = () => failure(INVALID_NECTAR_CARD_ERROR);

export const nectarCardApplied = nectarCardNumber => ({
  type: NECTAR_CARD_APPLIED,
  payload: { nectarCardNumber }
});

export const applyNectarCardFailed = error => failure(APPLY_NECTAR_CARD, error);

export const showCartDetails = () => ({
  type: SHOW_CART_DETAILS
});

export const hideCartDetails = () => ({
  type: HIDE_CART_DETAILS
});
