import React from 'react';
import PropTypes from 'prop-types';
import LevelOneItem from './LevelOneItem';
import Item from './Item';
import UtilityLink from './UtilityLink';

const List = ({ data, onItemClick }) => (
  <div className='tu-c-mobile-nav__list-container--list ln-u-soft'>
    {
      data.map((item) => {
        switch (item.type) {
          case 'navigation':
            return item.parentId === 0 ? (<LevelOneItem key={item.id} item={item} onItemClick={onItemClick} />)
              : (<Item key={item.id} item={item} onItemClick={onItemClick} />);
          case 'utility':
            return (<UtilityLink key={item.id} item={item} />);
          default:
            return null;
        }
      })
    }
  </div>
);

List.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    parentId: PropTypes.number,
    type: PropTypes.oneOf(['navigation', 'utility']),
    text: PropTypes.string,
    url: PropTypes.string
  })).isRequired,
  onItemClick: PropTypes.func.isRequired
};

export default List;
