import React from 'react';
import PropTypes from 'prop-types';

const Icon = ({
  classes, viewBox, icon, stroke, fill
}) => (
  <svg role='presentation' version='1.1' className={classes} viewBox={viewBox}>
    <use
      xmlnsXlink='http://www.w3.org/1999/xlink'
      xlinkHref={`/_ui/2017/symbol/svg/sprite.symbol.svg#${icon}`}
      stroke={stroke}
      fill={fill}
    />
  </svg>
);

Icon.propTypes = {
  classes: PropTypes.string.isRequired,
  viewBox: PropTypes.string,
  icon: PropTypes.string.isRequired,
  stroke: PropTypes.string,
  fill: PropTypes.string
};

Icon.defaultProps = {
  viewBox: undefined,
  stroke: undefined,
  fill: undefined
};

export default Icon;
