import { call, put } from 'redux-saga/effects';
import getHelper from '../helpers/axiosGetHelper';
import config from '../config/fitAsExpected';
import { BAZAAR_VOICE_DATA_RECEIVED } from '../actions/bazaarVoiceActions';

/*
Todo: The processing of the response data should move to the fitAsExpectedReducer leaving
    just the actual request in this saga which should do a 'put' with the returned data as its
    payload. The 'put' action can then be watched for and the payload reused for things other
    than fitAsExpected, giving is better reuse and more flexibility.
    Do this when we do the BazaarVoice work.
 */

const tallyRatingsByFit = (accumulator, review) => {
  const { SecondaryRatings: { Fit_22: { Value: fitValue } = {} } = {} } = review;
  if (!accumulator[fitValue]) {
    accumulator[fitValue] = 0;
  }
  accumulator[fitValue] += 1;
  return accumulator;
};

const calculatePercentage = (reviews) => {
  const tally = reviews.reduce(tallyRatingsByFit, {});
  return config.fitRanges.map((fitRange) => {
    const countForRange = tally[fitRange.range];
    return {
      ...fitRange,
      total: countForRange || 0,
      percentage: Math.round((countForRange / reviews.length * 100) || 0)
    };
  });
};

const constructConsensusObject = (description, percentage) => ({ description, percentage });

const calculateConsensus = (categories) => {
  const isAboveConsensusThreshold = ({ percentage }) => percentage > config.consensusThreshold;
  const majorityFitCategory = categories.find(isAboveConsensusThreshold) || {};
  switch (majorityFitCategory.range) {
    case 1:
    case 2:
      return constructConsensusObject(config.tooSmall, majorityFitCategory.percentage);
    case 3:
      return constructConsensusObject(config.asExpected, majorityFitCategory.percentage);
    case 4:
    case 5:
      return constructConsensusObject(config.tooBig, majorityFitCategory.percentage);
    default:
      return constructConsensusObject(null, null);
  }
};

export const transform = (reviews) => {
  const categories = calculatePercentage(reviews);

  if (reviews.length > config.minReviewValue) {
    return { fitConsensusStats: categories, fitConsensus: calculateConsensus(categories) };
  }

  return { fitConsensusStats: categories, fitConsensus: constructConsensusObject(null, null) };
};

export function* getBazaarVoiceApiData({ payload: { productCode } }) {
  try {
    const bvUrl = config.endPointUrl(config.API_KEY_PROD, productCode);
    const { data: { Results: reviews } } = yield call(getHelper, bvUrl);
    const transformedData = yield call(transform, reviews);
    yield put({ type: BAZAAR_VOICE_DATA_RECEIVED, payload: transformedData });
  } catch (error) {
    yield put({ type: 'ERROR', error });
  }
}
