const extractDataAttributesFromElement = (element) => {
  let attributes = {};

  if (element) {
    // Convert a DOMStringMap to an Object
    attributes = { ...element.dataset };
  }

  return attributes;
};

const extractDataAttributesFromElementId = (
  document, elementId
) => extractDataAttributesFromElement(
  document.getElementById(elementId)
);

export {
  extractDataAttributesFromElement,
  extractDataAttributesFromElementId
};
