const initialState = {
  data: [],
  lookupCalled: false,
  loading: false
};

const addressLookupData = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_LOOKUP_CONTENT':
    return Object.assign({}, state, {
      data: action.addresses,
      lookupCalled: action.lookupCalled
    })
    case 'SET_LOOKUP_LOAD':
    return Object.assign({}, state, {
      loading: action.status
    })
    default:
      return state
  }
}

export default addressLookupData
