import 'slick-carousel';
import $ from 'jquery';
import slickVariables from './slickVariables';
import lightboxService from './lightbox';

const productCarousel = $('.js-product-carousel');
const productDescriptionCarousel = $('.js-product-description-carousel');
const homepageCarousel = $('.js-homepage-carousel');

const carouselService = (function () {
  return {
    init: function () {
      productCarousel.each(function (id, item) {
        $(this).slick(slickVariables.productCarousel(this));
      });

      productDescriptionCarousel.on('init', () => {
        lightboxService.attachZoomHandler();
      }).slick(slickVariables.productDescription);

      homepageCarousel.each(function (id, item) {
        $(this).slick(slickVariables.homepageCarousel(this));
      });
    }
  };
}());

export default carouselService;
