import React from 'react';
import ReactDOM from 'react-dom';

const mountSizeGuide = (document) => {
  const mountPoint = document.getElementById('size-guide');

  if (mountPoint) {
    return import(/* webpackChunkName: "SizeGuide" */ '../../components/SizeGuide/SizeGuideContainer')
      .then(({ default: SizeGuide }) => {
        ReactDOM.render(<SizeGuide />, mountPoint);
      });
  }

  return null;
};

export default mountSizeGuide;
