import addressTemplate from '../config/addressTemplate';

export function createFulfilmentInformation() {
  return {
    code: '',
    options: [],
    date: '',
    dates: []
  };
}

export function createDeliveryInformation(items = []) {
  return items.map((item) => {
    const setObject = {
      provider: item.supplier.code,
      deliveryType: addressTemplate.deliveryType,
      deliveryAddress: addressTemplate.deliveryAddress,
      errors: addressTemplate.errors,
      fulfilmentInformation: createFulfilmentInformation()
    };
    return setObject;
  });
}

export function createSingleDeliveryInformation() {
  return [{
    provider: 'Tu',
    deliveryType: addressTemplate.deliveryType,
    deliveryAddress: addressTemplate.deliveryAddress,
    errors: addressTemplate.errors,
    fulfilmentInformation: createFulfilmentInformation()
  }];
}

export function allowClickCollect(delivery) {
  return delivery.supplier !== 'Tu';
}
