import React from 'react';
import PropTypes from 'prop-types';
import Icon from '../../../presentational/Icon';

const Item = ({ item, onItemClick }) => {
  const {
    id, text, url, childIds
  } = item;

  return text
    ? (
      <a
        href={url}
        title={text}
        onClick={(e) => {
          if (childIds.length) {
            e.preventDefault();
            onItemClick(id);
          }
        }}
        className='ln-u-display-block ln-u-text-decoration-normal'
      >
        <div className='tu-c-mobile-nav__list-container--item
        ln-u-display-flex ln-u-align-items-center
        ln-u-push-top ln-u-soft ln-u-bg-color-white'
        >
          <div className='ln-u-display-flex ln-u-justify-content-space-between ln-u-1/1'>
            <div>{text}</div>
            <Icon classes='ln-c-icon' icon='chevlon-right' viewBox='-3 -3 12 12' />
          </div>
        </div>
      </a>
    )
    : null;
};

Item.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number,
    parentId: PropTypes.number,
    type: PropTypes.oneOf(['navigation']),
    text: PropTypes.string,
    url: PropTypes.string
  }).isRequired,
  onItemClick: PropTypes.func.isRequired
};

export default Item;
