const productGateway = ({ productGatewayUrl = '', productGatewayApikey = '' }) => {
  const argosProductCode = imagerySku => `tuc${imagerySku}`;
  const apiKeyParam = productGatewayApikey ? `?apiKey=${productGatewayApikey}` : '';

  return {
    endpoints: {
      media: id => `${productGatewayUrl}/product/${argosProductCode(id)}/media${apiKeyParam}`
    }
  };
};

const productApi = () => ({
  endpoints: {
    url: id => `/api/v2/product/${id}`
  }
});

export { productApi, productGateway };
