import { call, put } from 'redux-saga/effects';
import get from '../helpers/axiosGetHelper';
import config from '../config/storeLocation';
import { previousStoreReceived, requestPreviousStoreFailed } from '../actions/storeLocatorActions';

export function* fetchPreviousStore({ payload: { storeId, postcodeOrTown } }) {
  try {
    const { data: { stores } } = yield call(get, `${config.apiUrl}?location=${postcodeOrTown}`);
    const previousStore = stores.find(store => store.id === storeId);
    if (previousStore) {
      yield put(previousStoreReceived(previousStore));
    } else {
      yield put(requestPreviousStoreFailed());
    }
  } catch (error) {
    yield put(requestPreviousStoreFailed(error));
  }
}
