import React from 'react';
import PropTypes from 'prop-types';

const Icon = (props) => {
  const {
    classes, viewBox, icon, stroke, fill, strokeWidth
  } = props;

  return (
    <svg role='presentation' xmlns='http://www.w3.org/2000/svg' version='1.1' className={classes} viewBox={viewBox}>
      <use
        xmlnsXlink='http://www.w3.org/1999/xlink'
        xlinkHref={`/_ui/2017/symbol/svg/sprite.symbol.svg#${icon}`}
        stroke={stroke}
        strokeWidth={strokeWidth}
        fill={fill}
      />
    </svg>
  );
};

Icon.propTypes = {
  classes: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  viewBox: PropTypes.string,
  stroke: PropTypes.string,
  strokeWidth: PropTypes.string,
  fill: PropTypes.string
};

Icon.defaultProps = {
  viewBox: null,
  stroke: null,
  strokeWidth: '1',
  fill: null
};

export default Icon;
