const initialState = {
  selectedMethod: "",
  displayForm: false,
  paypal_status: 'unloaded',
  paypal_token: '',
  paypal_nonce: ''
};

const paymentType = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_PAYMENT_METHOD':
    return Object.assign({}, state, {
      selectedMethod: action.selected
    })
    case 'SET_DISPLAY_FORM':
    return Object.assign({}, state, {
      displayForm: action.display
    })
    case 'SET_PAYPAL_STATUS':
    return Object.assign({}, state, {
      paypal_status: action.status
    })
    case 'SET_PAYPAL_TOKEN':
    return Object.assign({}, state, {
      paypal_token: action.token
    })
    case 'SET_PAYPAL_NONCE':
    return Object.assign({}, state, {
      paypal_nonce: action.nonce
    })
    default:
      return state
  }
}

export default paymentType
