import React from 'react';
import ReactDOM from 'react-dom';

import MobileNav from '../../components/MobileNav/MobileNavContainer';

const mountMobileNav = (document) => {
  const mountPoint = document.getElementById('mobile-nav');

  if (mountPoint) {
    const { access } = mountPoint.dataset;
    const ref = (mobileNavigation) => { window.mobileNavigation = mobileNavigation; };

    ReactDOM.render(<MobileNav ref={ref} access={access} />, mountPoint);
  }
};

export default mountMobileNav;
