import React, { Component } from 'react';
import PropTypes from 'prop-types';

class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hasError: false
    };
  }

  static getDerivedStateFromError() {
    return {
      hasError: true
    };
  }

  render() {
    const { hasError } = this.state;
    const { children, errorMessage } = this.props;
    if (hasError) {
      return (
        <div className='ln-c-field-info--block has-error'>
          <p className='ln-c-field-info ln-u-display-block ln-u-flush-bottom ln-c-field-info--error'>
            <span className='ln-u-soft'>{errorMessage}</span>
          </p>
        </div>
      );
    }
    return children;
  }
}

ErrorBoundary.propTypes = {
  children: PropTypes.node,
  errorMessage: PropTypes.string
};

ErrorBoundary.defaultProps = {
  children: {},
  errorMessage: 'There has been an error, please try again later.'
};

export default ErrorBoundary;
