import baseDimensions from '../config/dimensions.json';
import { baseDensities } from '../config/defaults.json';

const hyphenate = text => text.replace(/\s+/g, '-');

const zeroPadLessThanTen = index => (index < 10 ? `0${index}` : `${index}`);

export const decorateAmplienceUrlWithSeoTerms = (url, productName, index) => (
  `${url}/${hyphenate(productName)}-${zeroPadLessThanTen(index)}`
);

const getBaseDimensions = (pageType = 'pdp') => {
  const { main, thumbnail, zoom } = baseDimensions;
  return {
    mainDimensions: main[pageType] || main.pdp,
    thumbnailDimensions: thumbnail[pageType] || thumbnail.pdp,
    zoomDimensions: zoom[pageType] || zoom.pdp
  };
};

const buildFormats = (imageUrl, dimensions, {
  formats = ['webp', 'pjpg', 'jpg'],
  densities = baseDensities,
  isAmplienceFormat = true
}) => {
  const formatsObject = {};

  formats.forEach((format) => {
    const urls = densities.map((density) => {
      let url = imageUrl;
      if (isAmplienceFormat) {
        const width = dimensions.width ? `&w=${dimensions.width * density}` : '';
        const height = dimensions.height ? `&h=${dimensions.height * density}` : '';
        url = `${encodeURI(imageUrl)}/?fmt=${format}${width}${height}`;
      }

      return { url, density: `${density}x`, width: dimensions.width };
    });

    formatsObject[format] = urls;
  });

  return formatsObject;
};

function buildAmplienceUrls(amplienceImages, productName, { pageType, ...options }) {
  const { mainDimensions, thumbnailDimensions, zoomDimensions } = getBaseDimensions(pageType);

  return amplienceImages.map((image, index) => {
    const imageUrl = decorateAmplienceUrlWithSeoTerms(image, productName, index);

    return {
      alt: productName,
      main: buildFormats(imageUrl, mainDimensions, options),
      thumbnail: buildFormats(imageUrl, thumbnailDimensions, options),
      zoom: buildFormats(imageUrl, zoomDimensions, options)
    };
  });
}

function buildBasicUrls(cdnImages, productName) {
  const { mainDimensions, thumbnailDimensions, zoomDimensions } = getBaseDimensions();

  const options = {
    formats: ['jpg'],
    densities: [1],
    isAmplienceFormat: false
  };

  return cdnImages.map(image => ({
    alt: productName,
    main: buildFormats(image.main, mainDimensions, options),
    thumbnail: buildFormats(image.thumb, thumbnailDimensions, options),
    zoom: buildFormats(image.zoom, zoomDimensions, options)
  }));
}


export const buildPrimaryImageUrls = (
  amplienceImages,
  productName,
  options = {}
) => ({
  imageUrls: buildAmplienceUrls(amplienceImages, productName, options)
});

export const buildSecondaryImageUrls = (cdnImages, productName) => ({
  imageUrls: buildBasicUrls(cdnImages, productName)
});

export const buildFallbackImageUrls = (fallbackImageUrl, productName) => ({
  imageUrls: buildBasicUrls([{
    main: fallbackImageUrl,
    thumb: fallbackImageUrl,
    zoom: fallbackImageUrl
  }],
  productName)
});
