/* eslint-disable func-names */
import $ from 'jquery';
import axios from 'axios';
import itemSwapService from './itemswap';
import metricsHelper from './metrics';
import errors from '../config/errors';
import config from './metrics/config/config';

const loadmoreService = (function () {
  return {
    loadMoreButton: '#showMore',
    loadMoreItem: '.js-loadmore',
    productGrid: '.js-product-items',
    errorMessage: `<div class="ln-u-push-left tu-u-background--white ln-c-field-info--block has-error">
                  <div class="ln-c-field-info ln-u-1/1 ln-c-field-info--error">
                  <div class="ln-u-display-inline-block ln-u-soft">
                  <p class="ln-u-h4 ln-u-flush-bottom">${errors.messages.loadMoreItems}</p>
                  </div>
                  </div>
                  </div>`,
    loading: `<div class="tu-c-spinner tu-c-spinner--active">
                <div class="tu-c-spinner__inner"></div>
              </div>`,
    scrolling: false,
    hasButton: false,
    loadingMechanism: function (button) {
      $(button).closest(loadmoreService.loadMoreItem).html(loadmoreService.loading);
      loadmoreService.setButtonStatus();
    },
    getData: function (ref) {
      return axios.get(ref)
        .then((response) => {
          $(loadmoreService.productGrid).find(loadmoreService.loadMoreItem).remove();
          $(loadmoreService.productGrid).append(response.data);
          loadmoreService.setButtonStatus();
          itemSwapService.updatelazyLoad();
          const metrics = loadmoreService.getMetricsData();
          const fetchUrl = `${metrics.url}${loadmoreService.getURLParams(ref)}`;
          metricsHelper.getMetrics(fetchUrl, metrics, true);
          return true;
        })
        .catch(() => {
          $(loadmoreService.productGrid).find(loadmoreService.loadMoreItem).remove();
          $(loadmoreService.productGrid).append(loadmoreService.errorMessage);
          return false;
        });
    },
    getMetricsData: function () {
      const plpMetrics = config.filter(metric => metric.type === 'plp-search');
      return plpMetrics && plpMetrics.length === 1 ? plpMetrics[0] : null;
    },
    getURLParams: function (ref) {
      const setRef = ref.split('?');
      return setRef[1];
    },
    getWindowPositions: function () {
      const scrollTop = $(window).scrollTop();
      const height = $(window).height();
      return scrollTop + height;
    },
    getOffset: function () {
      const item = $(loadmoreService.loadMoreButton).offset();
      return item.top;
    },
    setButtonStatus: function () {
      const status = $(document).find(loadmoreService.loadMoreButton).length > 0;
      this.hasButton = status;
    },
    isReadyForLoad: function () {
      let ready = false;
      if (this.getWindowPositions() >= this.getOffset()) {
        ready = true;
      }
      return ready;
    },
    init: function (remount = () => {}) {
      loadmoreService.setButtonStatus();

      $(document).on('click keypress touchend', this.loadMoreButton, function (e) {
        e.preventDefault();
        const queryUrl = $(this).data('ref');
        loadmoreService.loadingMechanism($(this));
        loadmoreService.getData(queryUrl)
          .then((success) => {
            if (success) {
              remount();
            }
          });
      });

      $(window).scroll(() => {
        if (loadmoreService.hasButton) {
          loadmoreService.scrolling = true;
        }
      });

      setInterval(() => {
        if (loadmoreService.scrolling) {
          loadmoreService.scrolling = false;
          if (loadmoreService.isReadyForLoad()) {
            $(document).find(loadmoreService.loadMoreButton).trigger('click');
          }
        }
      }, 100);
    }
  };
}());

export default loadmoreService;
