export const addHandlerToTealiumResetCookiePreferencesLink = (document, handler) => {
  const resetCookiesLinkElement = document.querySelector('[title="resetCookiePreferences"]');
  if (resetCookiesLinkElement !== null) {
    resetCookiesLinkElement.onclick = handler;
  }
};

export const handleTealiumResetCookiePreferencesClicked = window => (e) => {
  const {
    utag: {
      cfg: {
        domain = ''
      } = {}
    } = {}
  } = window;

  if (domain === '' || domain === null) {
    throw new Error('Unable to reset Tealium Consent Manager Cookie');
  }

  e.preventDefault();
  // eslint-disable-next-line no-param-reassign
  window.document.cookie = `CONSENTMGR=; path=/; domain=${domain}; expires=Thu, 01 Jan 1970 00:00:00 GMT`;
  window.location.reload();
};
