import { put } from 'redux-saga/effects';
import {
  postcodeOrTownValidated, patternMismatchError, valueMissingError
} from '../actions/storeLocatorActions';

export const isValid = (text) => {
  const pattern = '^[a-zA-Z0-9\\s\\-.,:/()]+$';
  return new RegExp(pattern).test(text);
};

export function* validatePostcodeOrTown({ payload: { postcodeOrTown } }) {
  if (!postcodeOrTown) {
    yield put(valueMissingError());
  } else if (isValid(postcodeOrTown)) {
    yield put(postcodeOrTownValidated(postcodeOrTown));
  } else {
    yield put(patternMismatchError());
  }
}
