import $ from 'jquery';

const accordionService = (function () {
  return {
    accordionPanelToggle: $('.js-accordion-toggle'),
    accordionToggle: '.js-accordion-toggle',
    accordionPanel: '.js-accordion',
    accordionPanelOpen: 'is-open',
    addClickEvent: function () {
      $(document).on('click', this.accordionToggle, function () {
        const $current = $(this);
        $current.parents(accordionService.accordionPanel).siblings()
          .removeClass(accordionService.accordionPanelOpen);
        $current.closest(accordionService.accordionPanel)
          .toggleClass(accordionService.accordionPanelOpen);
      });
    }
  };
}());

export default accordionService;
