export const hideBrandSizeGuide = (document) => {
  const container = document.getElementById('brand-size-guide-container');

  if (container) {
    container.classList.add('tu-brand-size-guide-container--hidden');
  }
};

export const getSizeGuideImageWidth = (type) => {
  switch (type) {
    case 'desktop':
      return 900;
    case 'mobile':
      return 375;
    default:
      return null;
  }
};
