import React from 'react';
import ReactDOM from 'react-dom';

const mountPaymentDetails = (store, document) => {
  const mountPoint = document.getElementById('paymentDetails');

  if (mountPoint) {
    return import(/* webpackChunkName: "PaymentDetails" */ './PaymentDetails.provider')
      .then(({ default: ProvidedPaymentDetails }) => {
        ReactDOM.render(<ProvidedPaymentDetails store={store} />, mountPoint);
      });
  }

  return null;
};

export default mountPaymentDetails;
