import React from 'react';
import ReactDOM from 'react-dom';

import { buildProps } from '../../helpers/sizeSelectorPropsBuilder';

const mountSizeSelector = (document) => {
  const mountPoint = document.getElementById('size-selector');

  if (mountPoint) {
    const { sizeString, numOfSizes, charCount } = mountPoint.dataset;

    return Promise.all([
      import(/* webpackChunkName: "SizeSelector" */ '../../components/SizeSelector'),
      import(/* webpackChunkName: "pdpService" */ '../../components/pdp')
    ])
      .then(([{ default: SizeSelector }, { default: pdpService }]) => {
        ReactDOM.render(<SizeSelector {...buildProps(sizeString, numOfSizes, charCount)} pdpService={pdpService} />, mountPoint);
      });
  }

  return null;
};

export default mountSizeSelector;
