const navService = (function () {
  return {
    navToggle: document.getElementsByClassName('js-nav-toggle'),
    navSortby: 'js-nav-toggle--sortby',
    navFilter: 'js-nav-toggle--filter',
    panelSortby: 'js-filter-sortby',
    panelFilter: 'js-filter-filterby',
    closeFilter: document.getElementsByClassName('js-filter-close'),
    navFiltersOpen: 'is-open',
    setOpenPanel: '',
    navOpen: 'nav-open',
    filterItem: document.getElementsByClassName('js-filter-item'),
    navSubNav: '.js-filter-panel',
    navSubNavOpen: document.getElementsByClassName('js-filter-panel nav-open'),
    navSubTitle: document.getElementsByClassName('js-filter-title'),
    facetTitle: document.getElementsByClassName('js-facet-title'),
    navSubBack: document.getElementsByClassName('js-filter-back'),
    sortSelect: document.getElementById('sortOptions1'),
    sortRadio: document.getElementsByClassName('js-sortby-radio'),
    mobileFilterQuery: document.getElementById('currentQuery'),
    mobileFilterItem: document.getElementsByClassName('js-mobile-facet'),
    mobileFilter: document.getElementById('mobileFilter'),
    showAllFilters: 'open-all',
    showPrimaryFilters: 'open-primary',
    showButtonText: {showmore: 'Show more', showless: 'Show less'},
    showAllButton: document.getElementsByClassName('js-show-all-filters'),
    currentQuery: '',
    queryUpdated: false,
    getFilterType: function (e = {}) {
      if (e.classList.contains(this.navSortby)) {
        return this.panelSortby
      }
      return this.panelFilter
    },
    setOpenItem: function (e = '') {
      this.setOpenPanel = e;
    },
    setCurrentQuery: function (query = '') {
      this.currentQuery = query;
    },
    setBackArrow: function (state = false) {
      if (this.navSubBack.length === 0) {
        return
      };
      this.navSubBack[0].disabled = !state;
    },
    submitForm: function (e = {}) {
      if ( e.tagName.toLowerCase() === 'form' ) {
        e.submit();
      } else {
        e.form.submit();
      }
    },
    setFilterOpen: function (e = {}) {
      this.addRemoveClass(e.closest(this.navSubNav), this.navOpen, 'add')
      this.setBackArrow(true);
    },
    setFilterClose: function () {
      if (this.navSubNavOpen.length === 0) {
        return
      }
      this.addRemoveClass(this.navSubNavOpen[0], this.navOpen, 'remove');
      this.setBackArrow(false);
    },
    changeShowAll: function (e = {}) {
      if (!e.dataset['expansion'] || !e.previousElementSibling.dataset['expansion']) {
        return false
      }
      const isShowAll = e.dataset['expansion'] === navService.showAllFilters;
      e.innerHTML = isShowAll ? this.showButtonText.showmore : this.showButtonText.showless;
      e.dataset['expansion'] = isShowAll ? this.showPrimaryFilters : this.showAllFilters;
      e.previousElementSibling.dataset['expansion'] = isShowAll ? this.showPrimaryFilters : this.showAllFilters;
    },
    evaluateQuery: function (e = {}) {
      const setID = e.target.id;
      const setName = e.target.dataset.facet;
      navService.queryUpdated = true;
      if( navService.isInQuery(`:${setName}:${setID}:`) ) {
        navService.removeFromQuery(`${setName}:${setID}:`)
      } else {
        navService.addToQuery(`${setName}:${setID}:`)
      }
    },
    isInQuery: function (item = '') {
      return this.currentQuery.includes(item);
    },
    setTitle: function (title='Filter') {
      if (this.facetTitle.length === 0) {
        return
      };
      this.facetTitle[0].innerHTML = title;
    },
    addRemoveClass: function (e={}, classTarget='', type='remove') {
      if (type === 'remove') {
        e.classList.remove(classTarget);
      } else {
        e.classList.add(classTarget);
      }
    },
    removeFromQuery: function (query = '') {
      const currentState = this.currentQuery;
      this.currentQuery = currentState.replace(query,'');
      this.updateCurrentQuery(this.currentQuery);
    },
    addToQuery: function (query = '') {
      const currentState = this.currentQuery;
      this.currentQuery = `${currentState}${query}`;
      this.updateCurrentQuery(this.currentQuery);
    },
    updateCurrentQuery: function (val) {
      this.mobileFilterQuery ? this.mobileFilterQuery.value = val : false;
    },
    init: function () {
      for (let i = 0; i < this.navToggle.length; i++) {
        this.navToggle[i].addEventListener('click', (e) => {
          e.preventDefault();
          const setPanel = navService.getFilterType(e.target);
          const isOpen = navService.setOpenPanel.length > 0 ? document.getElementsByClassName(navService.setOpenPanel) : [];

          navService.setOpenItem(setPanel);
          
          if (isOpen.length > 0) {
            navService.addRemoveClass(isOpen[0], navService.navFiltersOpen, 'remove');
          }
          navService.addRemoveClass(document.getElementsByClassName(setPanel)[0], navService.navFiltersOpen, 'add');
        });
      };

      for (let i = 0; i < this.closeFilter.length; i++) {
        this.closeFilter[i].addEventListener('click', (e) => {
          e.preventDefault();
          if (navService.queryUpdated) {
            navService.submitForm(navService.mobileFilter);
          } else {
            navService.addRemoveClass(document.getElementsByClassName(navService.setOpenPanel)[0], navService.navFiltersOpen, 'remove');
            navService.setOpenItem('');
            navService.setFilterClose();
          }
        });
      };

      for (let i = 0; i < this.showAllButton.length; i++) {
        this.showAllButton[i].addEventListener('click', (e) => {
          e.preventDefault();
          navService.changeShowAll(e.target);
        });
      };

      const formSubmitEvents = [this.sortSelect, this.filterItem, this.sortRadio];

      formSubmitEvents.forEach(function (item) {
        if (item) {
          for (let i = 0; i < item.length; i++) {
            item[i].addEventListener('click', (e) => {
              navService.submitForm(e.target);
            });
          };
        };
      });

      if (this.sortSelect) {
        this.sortSelect.addEventListener('change', (e) => {
          navService.submitForm(e.target);
        });
      };

      for (let i = 0; i < this.navSubTitle.length; i++) {
        this.navSubTitle[i].addEventListener('click', (e) => {
          const type = e.target.dataset['facetType'] ? e.target.dataset['facetType'] : '';
          e.preventDefault();
          navService.setFilterOpen(e.target);
          navService.setTitle(type);
        });
      }

      for (let i = 0; i < this.navSubBack.length; i++) {
        this.navSubBack[i].addEventListener('click', (e) => {
          e.preventDefault();
          navService.setFilterClose();
          navService.setTitle();
        });
      }

      for (let i = 0; i < this.mobileFilterItem.length; i++) {
        this.mobileFilterItem[i].addEventListener('change', navService.evaluateQuery);
      }

      this.mobileFilterQuery ? this.setCurrentQuery(navService.mobileFilterQuery.value) : false;
    }
  };
}());

export default navService;
