import React from 'react';
import ReactDOM from 'react-dom';

import CPMEmailSignUp from '../../components/CPMEmailSignUp/CPMEmailSignUp.component';

const mountCPMEmailSignUp = (document) => {
  const mountPoint = document.getElementById('js-cpm-email-sign-up');

  if (mountPoint) {
    ReactDOM.render(<CPMEmailSignUp />, mountPoint);
  }
};

export default mountCPMEmailSignUp;
