const passwordShowHide = {
  init() {
    const showHideLinkEl = document.querySelectorAll('.js-show-hide');

    showHideLinkEl.forEach((element) => {
      element.addEventListener('click', (event) => {
        event.preventDefault();
        const getPasswordField = this.getPasswordField(event.currentTarget);
        const getButtonText = this.getTextItem(event.currentTarget);
        if (getPasswordField && getButtonText) {
          this.updatePasswordFieldStatus(getPasswordField, getButtonText);
        }
      });
    });
  },
  getPasswordField(passwordEvent) { return passwordEvent && passwordEvent.previousSibling ? passwordEvent.previousSibling : false ;},
  getTextItem(passwordEvent) {
    const showHideItem = passwordEvent.querySelector('.js-show-hide-text');
    return showHideItem || false;
  },
  passwordStatus(passwordItem) { return passwordItem && passwordItem.type && passwordItem.type === 'password' ? { type: 'text', text: 'Hide' } : { type: 'password', text: 'Show' }; },
  updatePasswordFieldStatus(passwordField, textElement) {
    const textEl = textElement;
    const passwordData = this.passwordStatus(passwordField);

    if (passwordField && passwordField.setAttribute) {
      passwordField.setAttribute('type', passwordData.type);
    }
    textEl.innerHTML = passwordData.text;
  }
};

export default passwordShowHide;
