import $ from 'jquery';

const tabsService = (function () {
  return {
    $tabs: '.js-tabs',
    $tabsPanel: '.js-tab-panel',
    $tabLinkEvent: $('.js-tab-link'),
    $tabList: '.js-tab-list',
    $tabLink: '.js-tab-link',
    $active: 'is-active',
    $hidden: 'ln-u-hidden',
    $ariaHidden: 'aria-hidden',

    tabClick: function (e) {
      const $target = $(e.target);
      const targetHref = $target.attr('href');

      $target
        .closest(this.$tabList)
        .find(this.$tabLink)
        .removeClass(this.$active)
        .attr({
          tabindex: -1,
          'aria-selected': false,
        });
      $target
        .addClass(this.$active)
        .attr({
          tabIndex: 0,
          'aria-selected': true,
        });
      $target.closest(this.$tabs).find(this.$tabsPanel)
        .addClass(this.$hidden)
        .attr(this.$ariaHidden, true);
      $(targetHref)
        .removeClass(this.$hidden)
        .attr(this.$ariaHidden, false);
    },
    addClickEvent: function () {
      // eslint-disable-next-line prefer-arrow-callback
      $(document).on('click', this.$tabLink, function (e) {
        e.preventDefault();
        tabsService.tabClick(e);
      });
    }
  };
}());

export default tabsService;
