import axios from 'axios';
import tealium from './tealium';
import peerius from './peerius';
import config from './config/config';

const extractPeeriusTrackingUrl = (metrics) => {
  const { node } = metrics;

  // Get dom node
  const element = node && node.id && document.getElementById(node.id);
  if (element) {
    // Get url attribute
    return element.getAttribute('data-peerius-tracking-url');
  }

  return false;
};

const metricsHelper = {
  init: () => {
    // Initialize all metrics
    config.forEach((metrics) => {
      metricsHelper.initializeMetrics(metrics);
    });
  },

  initializeMetrics: (metrics = {}) => {
    const { node, url } = metrics;

    // Get dom node
    const element = node.id ? document.getElementById(node.id) : null;
    if (element) {
      // Get node attribute
      const value = node.attr ? element.getAttribute(node.attr) : null;

      // Create fetch url for axios
      const fetchUrl = value ? `${url}${value}` : url;

      metricsHelper.getMetrics(fetchUrl, metrics);
    }
  },

  getMetrics: (fetchUrl = '', metrics = {}, isLazyLoad = false) => {
    const { type, tealiumAttributes, peeriusAttributes } = metrics;
    axios.get(fetchUrl)
      .then((response) => {
        const { data } = response;
        if (data) {
          // Trigger metrics
          if (tealiumAttributes) {
            tealium.trigger({
              type, ...tealiumAttributes, data, isLazyLoad
            });
          }

          if (peeriusAttributes) {
            const trackingUrl = extractPeeriusTrackingUrl(metrics);
            if (trackingUrl) {
              peerius.trigger({ type, data, trackingUrl });
            } else {
              console.warn(`Peerius Metrics missing tracking url: type[${type}]`);
            }
          }
        }
      })
      .catch((err) => {
        console.warn('Error', err);
      });
  }
};

export default metricsHelper;
