import React from 'react';
import ReactDOM from 'react-dom';

const mountBrandLogo = (document) => {
  /* Note querySelectorAll returns a non-live element list, rather than a live one.
  So the underlying data structure won't change as after being assigned to the variable */
  const brandLogoMountElements = document.querySelectorAll('[data-mount-point="brand-logo"]');

  if (brandLogoMountElements && brandLogoMountElements.length > 0) {
    return import(/* webpackChunkName: "BrandLogo" */ '../../components/BrandLogo')
      .then(({ default: BrandLogo }) => {
        brandLogoMountElements.forEach((mountpoint) => {
          if (mountpoint.children.length === 0) {
            const { brandName, brandUrl } = mountpoint.dataset;

            ReactDOM.render(<BrandLogo brandName={brandName} brandUrl={brandUrl} />, mountpoint);
          }
        });
      });
  }

  return null;
};

export default mountBrandLogo;
