/* global window */
import $ from 'jquery';
import axios from 'axios';

const pdpService = (function () {
  return {
    $productVariantColour: $('#productVariantColour .ln-c-form-option__input'),
    $productVariantColourSelect: $('#productVariantColour .ln-c-form-option__input:checked'),
    $productVariantSize: $('#productVariantSize'),
    $submitButton: $('#AddToCart'),
    $maxQntyExceeded: $('#maxQntyExceeded'),
    $addToCartError: $('#addToCartError'),
    $addToCart: $('.js-add-to-cart'),
    $productColour: false,
    $productSize: false,
    $productQty: false,
    $isValid: false,
    $jsonProductRequest: '/cart/jsonProductRequest',
    $qtyItem: $('#productVariantQty'),
    $qtyField: $('#qty'),
    $qtyContainer: $('#variantQty'),
    $sizeContainer: $('#variantSize'),
    $colourContainer: $('#variantColour'),
    $sizeText: $('.js-product-size'),
    $colourText: $('.js-product-colour'),
    $priceText: $('.js-set-price'),
    $priceWasText: $('.js-set-was-price'),
    $priceWasWasText: $('.js-set-was-was-price'),
    $jsonProductCodePost: $('#jsonProductCodePost'),
    $productReviewsTab: $('#tu-product-reviews'),
    $productReviewsTabHeader: $('#tu-product-reviews').parents('.js-foldable'),
    $BVWrapper: $('.tu-pdp__bvrr'),
    $BVLink: $('.bv-stars-container'),
    _toggleSubmit: function (status = true) {
      this.$submitButton.prop('disabled', status);
    },
    loadColorVariant: function (setURL) {
      if (setURL) {
        window.location = setURL;
      }
    },
    checkColorVariant: function () {
      if (this.$productVariantColourSelect.length > 0) {
        const color = this.$productVariantColourSelect.prop('id');
        this.setText(this.$colourText, color);
        this.$productColour = true;
      }
    },
    checkSizeVariant: function () {
      const value = this.$productVariantSize.val();
      if (value) {
        const text = this.$productVariantSize.find(`option[value='${value}']`).text();
        pdpService.setSize(value, text);
      }
    },
    checkErrors: function () {
      this.setErrors(this.$productColour, this.$colourContainer);
      this.setErrors(this.$productSize, this.$sizeContainer);
      this.setErrors(this.$productQty, this.$qtyContainer);
    },
    setErrors: function (item, container) {
      if (!item) {
        container.addClass('has-error');
      } else {
        container.removeClass('has-error');
      }
    },
    checkFormValid: function () {
      if (this.$productColour && this.$productSize && this.$productQty) {
        this.$isValid = true;
        this._toggleSubmit(false);
      } else {
        this.$isValid = false;
        this._toggleSubmit(true);
      }
    },
    setSize: function (val = 0, text = '') {
      this.setText(this.$sizeText, text);
      if (val) {
        this.$productSize = true;
        this.setErrors(this.$productSize, this.$sizeContainer);
        this.getQty(val);
      }
    },
    setText: function (item, text) {
      item.text(text);
    },
    setSKUsize: function (sku) {
      this.$jsonProductCodePost.val(sku);
    },
    setPrice: function (data) {
      const setData = data.data.price;
      // eslint-disable-next-line max-len
      const price = setData.formattedPromotionPrice ? setData.formattedPromotionPrice : setData.formattedValue;
      this.$priceText.text(price);
      if (setData.formattedWasPrice) {
        this.$priceWasText.text(setData.formattedWasPrice);
      }
      if (setData.formattedWasWasPrice) {
        this.$priceWasWasText.text(setData.formattedWasWasPrice);
      }
    },
    getQty: function (val) {
      axios.get(this.$jsonProductRequest, {
        params: {
          skusize: val
        }
      })
        .then((response) => {
          pdpService.populateQty(response);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeQty: function (val = 1) {
      this.$qtyField.val(val).prop('disabled', false);
      this.$maxQntyExceeded.hide();
      this.$addToCartError.hide();
    },
    populateQty: function (data = null) {
      const qty = data.data.productDropdownQuantity;
      const sku = data.data.code;
      this.setSKUsize(sku);
      this.setPrice(data);
      if (typeof qty === 'number') {
        this.$qtyItem.empty().prop('disabled', false);
        for (let i = 1; i < qty + 1; i += 1) {
          this.$qtyItem.append($('<option></option>').val(i).html(i));
        }

        if (qty > 0) {
          this.changeQty(1);
          this.$productQty = true;
          this.$productSize = true;
          this.$maxQntyExceeded.hide();
          this.$addToCartError.hide();
        } else {
          this.$productSize = false;
          this.$productQty = false;
        }
        this.checkErrors();
        this.checkFormValid();
      }
    },
    disabledButtonEvent: function () {
      this.$submitButton.on('click', () => {
        pdpService.checkErrors();
      });
    },
    showAndOpenReviews: function () {
      this.$BVWrapper.on('click', this.$BVLink, function() {
        pdpService.$productReviewsTabHeader.addClass('is-open');
        $('html, body').animate({
          scrollTop: pdpService.$productReviewsTab.offset().top
        }, 1000);
      });
    },
    init: function () {
      this.checkColorVariant();
      this.checkSizeVariant();
      this.checkFormValid();
      this.disabledButtonEvent();
      this.showAndOpenReviews();
    }
  };
}());

export default pdpService;
