const config = {
  premiumDelivery: 'premium-delivery',
  homeDelivery: 'HOME_DELIVERY',
  collection: 'CLICK_AND_COLLECT',
  locale: 'en-GB',
  dayFormatConfig: { weekday: 'short' },
  monthFormatConfig: { month: 'short' },
  fulfilmentDateTitle: 'Choose your delivery date',
  storeCardProps: {
    nextDayCCText: 'Next Day Click & Collect isnt currently available from this store.',
    storeDetailsText: 'Store details',
    buttonText: 'Select'
  },
  getFormatter(formatConfig) {
    return new Intl.DateTimeFormat(this.locale, formatConfig).format;
  },
  getParsedDate(date) {
    const dayFormatter = this.getFormatter(this.dayFormatConfig);
    const monthFormatter = this.getFormatter(this.monthFormatConfig);
    return {
      day: dayFormatter(date),
      month: monthFormatter(date),
      date: date.getDate()
    };
  },
  getParsedDates(dates) {
    return dates.reduce((acc, value, index, arr) => {
      const { date: dateString, ...rest } = value;
      const date = new Date(dateString);
      acc.push({ ...this.getParsedDate(date), serverDate: dateString, ...rest });

      // If current date in iterator is Saturday and if not last item in array, add Sunday to the output array
      if (date.getDay() === 6 && index !== arr.length - 1) {
        date.setDate(date.getDate() + 1);
        acc.push({ ...this.getParsedDate(date), availability: false, serverDate: date.toISOString() });
      }
      return acc;
    }, []);
  },
  getUrlToFetchFulfilmentOptions({ cartId = '', type = '', value = '' }) {
    switch (type) {
      case this.homeDelivery:
        return `/cart/${cartId}/fulfilment-options/${type}?postalCode=${value}`;
      case this.collection:
        return `/cart/${cartId}/fulfilment-options/${type}?storeId=${value}`;
      default:
        return '';
    }
  },
  getUrlToEditCartWithFulfilmentType({ cartId = '', type = '', csrf = '' }) {
    switch (type) {
      case this.homeDelivery:
      case this.collection:
        return `/cart/${cartId}/delivery-options/${type}?CSRFToken=${csrf}`;
      default:
        return '';
    }
  },
  getUrlToFetchFulfilmentDates({ cartId = '', type = '' }) {
    switch (type) {
      case this.homeDelivery:
      case this.collection:
        return `/cart/${cartId}/fulfilment-options/${type}/premium_delivery`;
      default:
        return '';
    }
  },
  getUrlToEditCartWithFulfilmentDate({
    cartId = '', type = '', csrf = '', selectedDate = ''
  }) {
    switch (type) {
      case this.homeDelivery:
      case this.collection:
        return `/cart/${cartId}/fulfilment-options/${type}/premium_delivery?CSRFToken=${csrf}&selectedDate=${selectedDate}`;
      default:
        return '';
    }
  }
};

export default config;
