import React from 'react';
import ReactDOM from 'react-dom';

const mountCheckoutStoreLocator = (store, document, onOverlayActive, onOverlayInactive) => {
  const mountPoint = document.getElementById('checkout-store-locator');

  if (mountPoint) {
    const { dataset: { apiKey, previousStoreId, previousStorePostcode } } = mountPoint;
    return import(/* webpackChunkName: "CheckoutStoreLocator" */ './CheckoutStoreLocator.provider')
      .then(({ default: ProvidedCheckoutStoreLocator }) => {
        ReactDOM.render(
          <ProvidedCheckoutStoreLocator
            store={store}
            apiKey={apiKey}
            previousStoreId={previousStoreId}
            previousStorePostcode={previousStorePostcode}
            onOverlayActive={onOverlayActive}
            onOverlayInactive={onOverlayInactive}
          />,
          mountPoint
        );
      });
  }

  return null;
};

export default mountCheckoutStoreLocator;
