import { put, select } from 'redux-saga/effects';

import { primaryProductMediaReceived, primaryProductMediaUnavailable } from '../actions/productMediaActions';
import getHelper from '../helpers/axiosGetHelper';
import { productGateway } from './helpers/endpointDetails';

const globalSettingsSelector = state => state.globalSettings;

export function* fetchPrimaryProductMediaData({ payload: { productCode, productName } }) {
  try {
    const globalSettings = yield select(globalSettingsSelector);
    const url = productGateway(globalSettings).endpoints.media(productCode);
    const response = yield getHelper(url);
    const { data = {} } = response.data;
    const { attributes = {} } = data;
    const { images = [] } = attributes;
    const { pageType } = globalSettings;

    if (images.length > 0) {
      yield put(primaryProductMediaReceived({
        productCode, productName, pageType, ...data
      }));
    } else {
      yield put(primaryProductMediaUnavailable(productCode, productName));
    }
  } catch (error) {
    yield put(primaryProductMediaUnavailable(productCode, productName));
  }
}
