import React from 'react';
import PropTypes from 'prop-types';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import Header from './Header';
import List from './List';

class MobileNavWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [0]
    };
  }

  navigateBack = () => {
    this.setState((prevState) => {
      const newHistory = [...prevState.history].splice(0, prevState.history.length - 1);
      return { history: newHistory };
    });
  }

  onItemClick = (itemId) => {
    this.setState(prevState => ({
      history: prevState.history.concat(itemId)
    }));
  }

  render() {
    const { active, data } = this.props;
    const { navNodes, links } = data;
    const { history } = this.state;
    const activeStyle = active ? 'active' : '';

    return (
      <div className={`tu-c-mobile-nav ln-u-display-flex ln-u-flex-direction-col ${activeStyle}`}>
        {
          history.length > 1
            ? <Header text={navNodes[history[history.length - 1]].text} onBackClick={this.navigateBack} />
            : null
        }
        <TransitionGroup
          className='tu-c-mobile-nav__list-container ln-u-flex-grow'
        >
          {
            history.map((level) => {
              let listData = Object.values(navNodes).filter(item => item.parentId === level);
              listData = level === 0 ? listData.concat(links) : listData;
              return (
                <CSSTransition key={level} timeout={500} classNames='slide'>
                  <List data={listData} onItemClick={this.onItemClick} />
                </CSSTransition>
              );
            })
          }
        </TransitionGroup>
      </div>
    );
  }
}

MobileNavWrapper.propTypes = {
  active: PropTypes.bool.isRequired,
  data: PropTypes.exact({
    navNodes: PropTypes.objectOf(PropTypes.shape({
      id: PropTypes.number,
      parentId: PropTypes.number,
      type: PropTypes.oneOf(['navigation']),
      text: PropTypes.string,
      url: PropTypes.string
    })),
    links: PropTypes.arrayOf(PropTypes.exact({
      id: PropTypes.string,
      type: PropTypes.oneOf(['utility']),
      text: PropTypes.string,
      url: PropTypes.string
    }))
  }).isRequired
};

export default MobileNavWrapper;
