const initialState = {
  values: {
    newTitle: '',
    newFirstName: '',
    newSurname: '',
    newContactNumber: '',
    addressDeliveryhouseNameOrNumber: '',
    addressPostcode: '',
    addressDeliveryLine1: '',
    addressDeliveryLine2: '',
    addressDeliveryTownCity: '',
    paymentCardId: '',
    termsAndConditionsId: false,
    country: 'GB'
  },
  errors: [
    'newTitle',
    'newFirstName',
    'newSurname',
    'addressDeliveryhouseNameOrNumber',
    'addressPostcode',
    'addressDeliveryLine1',
    'addressDeliveryTownCity',
    'termsAndConditionsId'
  ],
  manualForm: false,
  shippingAddressLoaded: false,
  validate: true,
  showCountry: false,
  countries: []
};

const formData = (state = initialState, action) => {
  switch (action.type) {
    case 'UPDATE_ADDRESS_FORM':
    return Object.assign({}, state, {
      values: Object.assign({}, state.values, {
        [action.name]: action.value
      })
    })
    case 'UPDATE_ADDRESSES':
    return Object.assign({}, state, {
      values: action.values
    })
    case 'UPDATE_ERRORS':
    return Object.assign({}, state, {
      errors: action.errors
    })
    case 'SET_SHOW_MANUAL_FORM':
    return Object.assign({}, state, {
      manualForm: action.display
    })
    case 'SET_SHIPPING_ADDRESS':
    return Object.assign({}, state, {
      shippingAddressLoaded: action.loaded
    })
    case 'SET_VALIDATION':
      return { ...state, validate: action.validate }
    case 'SHOW_COUNTRY':
      return { ...state, showCountry: action.showCountry }
    case 'SET_COUNTRIES':
      return { ...state, countries: action.countries }
    default:
      return state
  }
}

export default formData
