import React from 'react';
import ReactDOM from 'react-dom';

import { hideBrandSizeGuide, getSizeGuideImageWidth } from './helpers';

const defaultHideBrandSizeGuideHandler = () => hideBrandSizeGuide(document);

const mountBrandSizeGuide = (document, hideBrandSizeGuideHandler = defaultHideBrandSizeGuideHandler) => {
  const mountpoint = document.getElementById('brand-size-guide');

  if (mountpoint) {
    const { dataset: { sizeGuideImageUrl, jspTemplateType } } = mountpoint;
    const sizeGuideImageWidth = getSizeGuideImageWidth(jspTemplateType);

    return import(/* webpackChunkName: "BrandSizeGuide" */ '../../components/BrandSizeGuide')
      .then(({ default: BrandSizeGuide }) => {
        ReactDOM.render(
          <BrandSizeGuide
            sizeGuideImageUrl={sizeGuideImageUrl}
            hideBrandSizeGuide={hideBrandSizeGuideHandler}
            sizeGuideImageWidth={sizeGuideImageWidth}
          />,
          mountpoint
        );
      });
  }

  return null;
};

export default mountBrandSizeGuide;
