import { call, put } from 'redux-saga/effects';

import get from '../helpers/axiosGetHelper';
import config from '../config/orderSummary';
import { orderSummaryReceived, requestOrderSummaryFailed } from '../actions/orderSummaryActions';
import orderSummaryStub from './__stubs__/checkoutOrderSummaryStubs';

const noCacheHeaders = {
  'Cache-Control': 'no-cache',
  'Pragma': 'no-cache',
  'Expires': '-1'
};

export function* fetchOrderSummary() {
  try {
    const {
      data: {
        products, nectar, fulfilmentOption, deliveryAddress
      }
    } = yield call(get, config.apiUrl, null, noCacheHeaders);
    yield put(orderSummaryReceived({
      products,
      nectar,
      fulfilmentOption,
      deliveryAddress,
      ...orderSummaryStub
    }));
  } catch (error) {
    yield put(requestOrderSummaryFailed(error));
  }
}
