import React from 'react';
import ReactDOM from 'react-dom';

const mountCPMAccount = (document) => {
  const mountPoint = document.getElementById('CPM-account');

  if (mountPoint) {
    return import(/* webpackChunkName: "CPM" */ '../../components/CPM/CPM')
      .then(({ default: CPM }) => {
        ReactDOM.render(<CPM />, mountPoint);
      });
  }

  return null;
};

export default mountCPMAccount;
