import React from 'react';
import PropTypes from 'prop-types';

const Overlay = ({ active, onOverlayClick }) => {
  const activeStyle = active ? 'active' : '';
  let ref = null;

  const setRef = (el) => {
    ref = el;
  };

  const freezeScroll = (e) => {
    if (e.target === ref) {
      e.preventDefault();
    }
  };

  // Prevent body scroll and touchmove on overlay if active
  if (active) {
    document.body.classList.add('has-overlay');
    document.documentElement.classList.add('has-overlay');
    document.addEventListener('touchmove', freezeScroll, { passive: false });
  } else {
    document.body.classList.remove('has-overlay');
    document.documentElement.classList.remove('has-overlay');
    document.removeEventListener('touchmove', freezeScroll, { passive: false });
  }

  return (<div className={`tu-c-overlay ${activeStyle}`} ref={setRef} onClick={onOverlayClick} />);
};

Overlay.defaultProps = {
  active: false,
  onOverlayClick: null
};

Overlay.propTypes = {
  active: PropTypes.bool,
  onOverlayClick: PropTypes.func
};

export default Overlay;
