import React from 'react';
import ReactDOM from 'react-dom';

import LazyRender from '@tuui/lazy-render';
import '@tuui/lazy-render/dist/LazyRender.min.css';

import config from './config/lazyRender.json';

const createProps = (store, imagerySku, productName, zoomEnabled, suppressThumbnails) => {
  const props = { store, imagerySku, productName };
  if (zoomEnabled === 'false') props.zoomEnabled = false;
  if (suppressThumbnails === 'true') props.suppressThumbnails = true;
  return props;
};

const mountProductImageCarousel = (store, document) => {
  const mountpoints = document.getElementsByClassName('tu-c-productimagery-carousel');

  if (mountpoints.length) {
    return import(/* webpackChunkName: "ProvidedProductImageCarousel" */ './ProvidedProductImageCarousel')
      .then(({ default: ProvidedProductImageCarousel }) => {
        mountpoints.forEach((mountpoint) => {
          if (mountpoint.children.length) {
            return;
          }

          const {
            dataset: {
              imagerySku,
              zoomEnabled,
              suppressThumbnails,
              lazy,
              productName
            }
          } = mountpoint;

          if (lazy) {
            ReactDOM.render(
              <LazyRender id={`product-${imagerySku}`} activationMargin={config.activationMargin}>
                <ProvidedProductImageCarousel {...createProps(store, imagerySku, productName, zoomEnabled, suppressThumbnails)} />
              </LazyRender>,
              mountpoint,
            );
          } else {
            ReactDOM.render(
              <ProvidedProductImageCarousel {...createProps(store, imagerySku, productName, zoomEnabled, suppressThumbnails)} />,
              mountpoint,
            );
          }
        });
      });
  }

  return null;
};

export default mountProductImageCarousel;
