const util = {
  isIE: () => !!window.navigator.userAgent.match(/Trident\/7\./),

  getDataSet: (item, dataValue) => (item && item.dataset[dataValue] ? item.dataset[dataValue] : null),

  createMetricsData: ({
    type, eventName, eventType, data
  }) => {
    switch (type) {
      case 'cart':
        return util.createCartMetrics({
          type, eventName, eventType, data
        });
      case 'delivery':
        const metrics = util.createCartMetrics({
          type, eventName, eventType, data
        });

        const deliveryOptionID = document.getElementById('js-delivery-available');
        metrics[eventName].delivery_options = util.getDataSet(deliveryOptionID, 'deliveryOptions');
        return metrics;
      case 'sizeFit':
        return util.createSizeFitMetrics(data);
      case 'sizeFitPopUp':
        return util.createPopUpMetrics(data);
      case 'pdp':
        return util.createProductMetrics({
          type, eventName, eventType, data
        });
      case 'confirmation':
        return util.createConfirmationMetrics(data);
      case 'plp-search':
      case 'plp-category':
        const plpmetrics = util.createProductListMetrics({
          type, eventName, eventType, data
        });
        const plpID = document.getElementById('js-shownFilters');
        const breadCrumbs = document.getElementById('js-breadCrumbs');
        if (plpID && plpmetrics.eventName) { plpmetrics[eventName].applied_filters = util.getDataSet(plpID, 'filters'); }
        if (breadCrumbs && plpmetrics.eventName) { plpmetrics[eventName].breadcrumbs = util.getDataSet(breadCrumbs, 'breadcrumbs'); }
        return plpmetrics;
      case 'payment':
        return util.createPaymentPageMetrics({
          type, eventName, eventType, data
        });
      default:
        return {};
    }
  },

  getFitAsExpectedValues: (data, objectElement) => {
    // find and return in data object value from element matching objectElement arg
    const result = data.data.find(o => o.item === objectElement);
    return result ? result.total : null;
  },

  createPopUpMetrics: data => ({
    pop_up_active: data
  }),

  createSizeFitMetrics: (data) => {
    // Trigger loop if received data exists
    if (data) {
      //  Object to return and store in event
      return {
        visible: data.showLozenge,
        Rating: {
          overall_recommendation: data.selectedOption,
          overall_percentage: data.fitAsExpected,
          too_small: util.getFitAsExpectedValues(data, 'Too small'),
          a_little_small: util.getFitAsExpectedValues(data, 'A little small'),
          true_to_size: util.getFitAsExpectedValues(data, 'True to size'),
          a_little_big: util.getFitAsExpectedValues(data, 'A little big'),
          too_big: util.getFitAsExpectedValues(data, 'Too big')
        }
      };
    }

    return null;
  },

  getDeliveryOption: (data) => {
    let optionVal;
    const option = {};

    // convert NodeList to accessible array
    const stable = Array.from(data);

    stable.forEach((element) => {
      optionVal = element.dataset.clickcollect;
      option.optionVal = optionVal;
    });
    return option;
  },

  createPaymentPageMetrics: ({
    eventName, data
  }) => {
    const deliveryOption = document.querySelectorAll('#paymentDetails[data-clickcollect]');
    const products = util.convertToArray(data.entries, 'product');
    const entries = util.convertToArray(data.entries);
    const basePrices = util.convertToArray(data.entries, 'basePrice');
    const totalPrices = util.convertToArray(data.entries, 'totalPrice');
    const skuType = 'selected';
    const receivedData = util.getDeliveryOption(deliveryOption);
    const metricsData = {
      [eventName]: {
        product_sku: products.code ? products.code : null,
        product_style: products.baseProduct ? products.baseProduct : null,
        product_styledescription: products.name ? products.name : null,
        product_skudescription: products.images ? products.images.map(image => (image ? image[0].altText : null)) : null,
        product_quantity: entries.quantity ? entries.quantity : null,
        product_waswasprice: basePrices.wasWasPrice ? basePrices.wasWasPrice : null,
        product_wasprice: basePrices.wasPrice ? basePrices.wasPrice : null,
        product_nowprice: basePrices.value ? basePrices.value : null,
        product_itemtotal: totalPrices.value ? totalPrices.value : null,
        product_discounttype: util.getDiscountType(entries, data.appliedProductPromotions),
        product_discounttitle: util.getDiscountType(entries, data.appliedProductPromotions, 'description'),
        product_brand: products.manufacturer ? products.manufacturer : null,
        product_supplier: util.getProductBrands(data.entries),
        product_group: entries.promotionDescription ? entries.promotionDescription : null,
        product_department: null,
        product_class: null,
        product_subclass: null,
        product_size: util.getProductAttrs(entries.product, { skuType, attr: 'size' }),
        product_colour: util.getProductAttrs(entries.product, { skuType, attr: 'colour' }),
        basket_discount: util.getDiscountInfo(data.appliedOrderPromotions, 'description'),
        basket_discountvalue: util.getDiscountInfo(data.appliedOrderPromotions, 'value'),
        basket_subtotalwithdiscount: data.subTotalWithDiscounts && data.subTotalWithDiscounts.value ? data.subTotalWithDiscounts.value : null,
        basket_subtotalbeforepromotion: data.subTotalBeforePromotion && data.subTotalBeforePromotion.value ? data.subTotalBeforePromotion.value : null,
        basket_total: data.totalWithOutDelivery && data.totalWithOutDelivery.value ? data.totalWithOutDelivery.value : null,
        voucher_discount: data.orderDiscounts && data.orderDiscounts.value ? data.orderDiscounts.value : null,
        vouchers: null,
        delivery_choice: receivedData ? 'Click & Collect' : 'Home Delivery'
      }
    };

    return metricsData;
  },

  createCartMetrics: ({
    eventName, data
  }) => {
    const products = util.convertToArray(data.entries, 'product');
    const entries = util.convertToArray(data.entries);
    const basePrices = util.convertToArray(data.entries, 'basePrice');
    const totalPrices = util.convertToArray(data.entries, 'totalPrice');
    const skuType = 'selected';

    const metricsData = {
      [eventName]: {
        product_sku: products.code ? products.code : null,
        product_style: products.baseProduct ? products.baseProduct : null,
        product_styledescription: products.name ? products.name : null,
        product_skudescription: Array.isArray(products.images) ? products.images.map(image => (image ? image[0].altText : null)) : null,
        product_quantity: entries.quantity ? entries.quantity : null,
        product_waswasprice: basePrices.wasWasPrice ? basePrices.wasWasPrice : null,
        product_wasprice: basePrices.wasPrice ? basePrices.wasPrice : null,
        product_nowprice: basePrices.value ? basePrices.value : null,
        product_itemtotal: totalPrices.value ? totalPrices.value : null,
        product_discounttype: util.getDiscountType(entries, data.appliedProductPromotions),
        product_discounttitle: util.getDiscountType(entries, data.appliedProductPromotions, 'description'),
        product_brand: products.manufacturer ? products.manufacturer : null,
        product_supplier: util.getProductBrands(data.entries),
        product_group: entries.promotionDescription ? entries.promotionDescription : null,
        product_department: null,
        product_class: null,
        product_subclass: null,
        product_size: util.getProductAttrs(entries.product, { skuType, attr: 'size' }),
        product_colour: util.getProductAttrs(entries.product, { skuType, attr: 'colour' }),
        basket_discount: util.getDiscountInfo(data.appliedOrderPromotions, 'description'),
        basket_discountvalue: util.getDiscountInfo(data.appliedOrderPromotions, 'value'),
        basket_subtotalwithdiscount: data.subTotalWithDiscounts && data.subTotalWithDiscounts.value ? data.subTotalWithDiscounts.value : null,
        basket_subtotalbeforepromotion: data.subTotalBeforePromotion && data.subTotalBeforePromotion.value ? data.subTotalBeforePromotion.value : null,
        basket_total: data.totalWithOutDelivery && data.totalWithOutDelivery.value ? data.totalWithOutDelivery.value : null,
        voucher_discount: data.orderDiscounts ? data.orderDiscounts.value : null,
        vouchers: null
      }
    };

    return metricsData;
  },

  createProductMetrics: ({
    eventName, data
  }) => {
    const skuType = util.getSkuType(data.code);
    const productAttrsPayload = skuType === 'sku' ? [data] : data.variantOptions;

    const getSizes = ({ productAttrsPayload, skuType }) => {
      const sizes = util.getProductAttrs(productAttrsPayload, { skuType, attr: 'size' });
      return sizes && sizes.length ? [].concat.apply([], sizes) : null;
    };

    const metricsData = {
      [eventName]: {
        product_sku: data.code ? data.code : null,
        product_style: data.baseProduct ? data.baseProduct : null,
        product_skutype: skuType,
        product_skudescription: data.images && data.images[0].altText ? data.images[0].altText : null,
        product_styledescription: data.name ? data.name : null,
        product_waswasprice: data.price.wasWasPrice ? data.price.wasWasPrice : null,
        product_wasprice: data.price.wasPrice ? data.price.wasPrice : null,
        product_nowprice: data.price.value ? data.price.value : null,
        product_supplier: data.supplier.code ? data.supplier.code : null,
        product_brand: data.manufacturer ? data.manufacturer : null,
        product_group: null,
        product_department: null,
        product_class: null,
        product_subclass: null,
        product_size: util.getProductAttrs([].concat(data), { skuType: 'selected', attr: 'size' }).join(),
        product_colour: util.getProductAttrs([].concat(data), { skuType: 'selected', attr: 'colour' }).join(),
        product_stock: data.stock && data.stock.stockLevelStatus ? `${data.stock.stockLevelStatus.code}:${data.stock.stockLevelStatus.type}` : null,
        product_productSoonStatus: data.productSoonStatus ? data.productSoonStatus : null,
        sizes: getSizes({ productAttrsPayload, skuType }),
        sizes_stocklevel: null
      }
    };

    return metricsData;
  },

  createConfirmationMetrics: (data) => {
    const skuType = 'selected';
    const entries = util.convertToArray(data.entries);
    const products = util.convertToArray(data.entries, 'product');
    const basePrices = util.convertToArray(data.entries, 'basePrice');
    const totalPrices = util.convertToArray(data.entries, 'totalPrice');

    const getVouchers = (vouchers = []) => vouchers.map(voucher => ({
      basket_voucher_discounttype: voucher.name,
      basket_voucher_discountcode: voucher.voucherCode,
      basket_voucher_discountvalue: voucher.value,
      basket_voucher_discountvaluestring: voucher.valueString
    }));

    const metricsData = {
      order: {
        zip: data.deliveryAddress && data.deliveryAddress.postalCode ? data.deliveryAddress.postalCode : null,
        quantity: data.totalUnitCount ? data.totalUnitCount : null,
        purchaseId: data.code ? data.code : null,
        guid: data.guid ? data.guid : null,
        trans_id: data.tlgTransactionRefNum ? data.tlgTransactionRefNum : null,
        product_sku: products.code ? products.code : null,
        product_style: products.baseProduct ? products.baseProduct : null,
        product_styledescription: products.name ? products.name : null,
        product_skudescription: products.images.map(image => (image ? image[0].altText : null)),
        product_quantity: entries.quantity ? entries.quantity : null,
        product_waswasprice: basePrices.wasWasPrice ? basePrices.wasWasPrice : null,
        product_wasprice: basePrices.wasPrice ? basePrices.wasPrice : null,
        product_nowprice: basePrices.value ? basePrices.value : null,
        product_itemtotal: totalPrices.value ? totalPrices.value : null,
        product_discounttype: util.getDiscountType(entries, data.appliedProductPromotions),
        product_discounttitle: util.getDiscountType(entries, data.appliedProductPromotions, 'description'),
        product_supplier: util.getProductBrands(data.entries),
        product_brand: products.manufacturer ? products.manufacturer : null,
        product_group: null,
        product_department: null,
        product_class: null,
        product_subclass: null,
        product_size: util.getProductAttrs(entries.product, { skuType, attr: 'size' }),
        product_colour: util.getProductAttrs(entries.product, { skuType, attr: 'colour' }),
        basket_discount: util.getDiscountInfo(data.appliedOrderPromotions, 'description'),
        basket_discountvalue: util.getDiscountInfo(data.appliedOrderPromotions, 'value'),
        basket_subtotal: data.subTotalWithDiscounts && data.subTotalWithDiscounts.value ? data.subTotalWithDiscounts.value : null,
        basket_total: data.totalPrice && data.totalPrice.value ? data.totalPrice.value : null,
        basket_paymenttype: data.paymentInfo && data.paymentInfo.cardType ? data.paymentInfo.cardType : null,
        basket_deliverytype: `${data.deliveryMode.code.includes('delivery') ? 'Home Delivery' : 'Click & Collect'} - ${data.deliveryMode.name} - ${data.deliveryMode.deliveryCost.value}`,
        basket_deliveryvalue: data.deliveryMode && data.deliveryMode.deliveryCost && data.deliveryMode.deliveryCost.value ? data.deliveryMode.deliveryCost.value : null,
        checkout_userstatus: data.guestCustomer ? 'Guest' : 'Registered',
        nectarcardnumber: data.nectarCardNumber ? data.nectarCardNumber : null,
        user_email: data.paymentInfo && data.paymentInfo.billingAddress && data.paymentInfo.billingAddress.email ? data.paymentInfo.billingAddress.email : null,
        voucher_discount: data.orderDiscounts && data.orderDiscounts.value ? data.orderDiscounts.value : null,
        vouchers: getVouchers(data.appliedVouchers)
      }
    };
    return metricsData;
  },

  createProductListMetrics: ({
    eventName, data
  }) => {
    if (data.results && data.results.length && data.pagination) {
      const results = util.convertToArray(data.results);
      const productColours = util.getProductAttrs(data.results, { skuType: 'sku', attr: 'colour' });
      const metricsData = {
        [eventName]: {
          pagination_pagenumber: typeof data.pagination.currentPage === 'number' ? data.pagination.currentPage : null,
          pagination_pagesize: typeof data.pagination.pageSize === 'number' ? data.pagination.pageSize : null,
          pagination_pagesort: typeof data.pagination.sort === 'string' ? data.pagination.sort : null,
          pagination_pagetotal: typeof data.pagination.totalNumberOfResults === 'number' ? data.pagination.totalNumberOfResults : null,
          pagination_numberofpages: typeof data.pagination.numberOfPages === 'number' ? data.pagination.numberOfPages : null,
          product_sku: results.code ? results.code : null,
          product_style: results.baseProduct ? results.baseProduct : null,
          product_skutype: results.code ? results.code.map(skuCode => util.getSkuType(skuCode)) : null,
          product_styledescription: results.name ? results.name : null,
          product_skudescription: results.images ? results.images.map(image => (image ? image[0].altText : null)) : null,
          product_position: data.results ? data.results.map((result, index) => index + 1) : null,
          product_waswasprice: results.price ? results.price.map(itemPrice => (itemPrice && itemPrice.wasWasPrice ? itemPrice.wasWasPrice : null)) : null,
          product_wasprice: results.price ? results.price.map(itemPrice => (itemPrice && itemPrice.wasPrice ? itemPrice.wasPrice : null)) : null,
          product_nowprice: results.price ? results.price.map(itemPrice => (itemPrice && itemPrice.value ? itemPrice.value : null)) : null,
          product_rating: null,
          product_supplier: results.supplier ? results.supplier.map(itemSupplier => (itemSupplier && itemSupplier.code ? itemSupplier.code : null)) : null,
          product_brand: results.manufacturer ? results.manufacturer.map(itemManufacturer => (itemManufacturer || null)) : null,
          product_group: null,
          product_department: null,
          product_class: null,
          product_subclass: null,
          product_size: null,
          product_colourvariants: productColours ? productColours.map(colour => (!!(colour && colour.length > 1))) : null,
          product_colour: productColours ? productColours.map(colour => (colour && colour.length ? colour[0] : null)) : null,
          product_stock: results.stock ? results.stock.map(itemStock => (itemStock && itemStock.stockLevelStatus && itemStock.stockLevelStatus.type ? `${itemStock.stockLevelStatus.code}:${itemStock.stockLevelStatus.type}` : null)) : null,
          product_productSoonStatus: results.expectedSoon ? results.expectedSoon : null,
        }
      };

      return metricsData;
    }
    return {};
  },

  getSkuType: (code = '') => (isNaN(code) ? 'style' : 'sku'),

  saveAndTriggerEvent: ({ key, metricsData, customEvent }) => {
    // Save to global object
    window.aSync_dataobject = key ? { ...window.aSync_dataobject, [key]: metricsData } : window.aSync_dataobject;

    // Create and dispatch the custom trigger
    if (util.isIE()) {
      // IE 11
      const event = document.createEvent('CustomEvent');
      event.initCustomEvent(customEvent, false, false, undefined);
    } else {
      document.dispatchEvent(new CustomEvent(customEvent));
    }
  },

  convertToArray: (arr = [], key = '') => {
    const output = {};
    const parse = (item = {}) => {
      Object.keys(item).forEach((key) => {
        if (output[key] && output[key].length) {
          output[key] = [...output[key], item[key]];
        } else {
          output[key] = [item[key]];
        }
      });
    };
    arr.length && arr.forEach((item) => { key ? parse(item[key]) : parse(item); });
    return output;
  },

  // Get product_colour, product_size attributes
  getProductAttrs: (arr = [], param) => {
    const { skuType, attr } = param;
    switch (skuType) {
      case 'sku':
        return arr.map((product) => {
          if (product && product.baseOptions && product.baseOptions.length) {
            const baseOption = product.baseOptions[0];
            const options = baseOption ? baseOption.options : null;
            return options && options.length ? options.map((option) => {
              if (option && option.variantOptionQualifiers && option.variantOptionQualifiers.length) {
                const setAttr = option.variantOptionQualifiers.find(o => o.qualifier === attr);
                if (setAttr) {
                  return setAttr.value;
                }
              }
            }) : [];
          }
        });
      case 'style':
        return arr.map((variantOption) => {
          if (variantOption && variantOption.variantOptionQualifiers && variantOption.variantOptionQualifiers.length) {
            const setAttr = variantOption.variantOptionQualifiers.find(o => o.qualifier === attr);
            if (setAttr) {
              return setAttr.value;
            }
          }
        });
      case 'selected':
        return arr.map((product) => {
          if (product && product.baseOptions && product.baseOptions.length) {
            const baseOption = product.baseOptions[0];
            const selected = baseOption ? baseOption.selected : null;
            if (selected && selected.variantOptionQualifiers && selected.variantOptionQualifiers.length) {
              const setAttr = selected.variantOptionQualifiers.find(o => o.qualifier === attr);
              if (setAttr) {
                return setAttr.value;
              }
            }
          }
        });
      default:
        return [];
    }
  },

  // Get discount description, value for applied product/order promotions
  getDiscountInfo: (appliedPromotions = [], param = '') => {
    switch (param) {
      case 'description':
        return appliedPromotions.map(promotion => (promotion && promotion.promotionData && promotion.promotionData.description ? promotion.promotionData.description : null));
      case 'value':
        return appliedPromotions.map(promotion => (promotion && promotion.totalDiscount && promotion.totalDiscount.value
          ? promotion.totalDiscount.value : null));
      default:
        return [];
    }
  },

  // Get product_supplier attribute
  getProductBrands: (entries = []) => entries.map(entry => (entry && entry.product && entry.product.supplier && entry.product.supplier.code ? entry.product.supplier.code : null)),

  // Get product_discounttype attribute
  getDiscountType: (entries = [], appliedPromotions = null, type = 'title') => (!appliedPromotions ? null : entries.entryNumber && entries.entryNumber.map(entryIndex => appliedPromotions.reduce((descAcc, promotion) => {
    switch (type) {
      case 'description':
        return !descAcc ? (promotion.consumedEntries.find(consumedEntry => consumedEntry.orderEntryNumber
              && consumedEntry.orderEntryNumber === entryIndex)
          ? promotion.description : null) : descAcc;
      default:
        return !descAcc ? (promotion.consumedEntries.find(consumedEntry => consumedEntry.orderEntryNumber
              && consumedEntry.orderEntryNumber === entryIndex)
          ? promotion.promotionData.title : null) : descAcc;
    }
  }, null)))
};

const tealium = {
  trigger: ({
    type, eventName, eventType, data, isLazyLoad = false
  }) => {
    // Create metrics data
    const metricsData = util.createMetricsData({
      type, eventName, eventType, data
    });

    switch (type) {
      case 'cart':
      case 'delivery':
      case 'plp-search':
      case 'plp-category':
      case 'pdp':
      case 'payment':
      case 'confirmation':
        util.saveAndTriggerEvent({ key: 'event', metricsData, customEvent: 'aSyncDataLoaded' });
        if (type === 'plp-search' && isLazyLoad) { window.aSyncCall ? window.aSyncCall('aSyncLoadSearch', '', '') : null; }
        if (type === 'plp-category' && isLazyLoad) { window.aSyncCall ? window.aSyncCall('aSyncLoadPLP', '', '') : null; }
        break;
      case 'sizeFit':
        util.saveAndTriggerEvent({ key: 'sizeFit', metricsData, customEvent: 'aSyncDataFitLoaded' });
        break;
      case 'sizeFitPopUp':
        util.saveAndTriggerEvent({ key: 'sizeFitPopUp', metricsData, customEvent: 'aSyncDataFitPopUp' });
        break;
      case 'sizeGuide':
        util.saveAndTriggerEvent({ customEvent: 'aSyncDataSizeGuide' });
        break;
      default:
    }
  }
};

export default tealium;
