import config from './config';

const util = {

  normalize: (data) => {
    const normalizedData = {};
    let counter = -1;

    const transform = ({ node, parentId }) => {
      counter += 1;
      const id = counter;
      const {
        children, links, title, linkName, ...rest
      } = node;
      const text = title || linkName;
      normalizedData[id] = {
        text,
        ...rest,
        id,
        parentId,
        childIds: [],
        type: 'navigation'
      };
      if (parentId >= 0) {
        normalizedData[parentId].childIds.push(id);
      }
      if (children && children.length) {
        children.forEach((childNode) => {
          transform({ node: childNode, parentId: id });
        });
      } else if (links && links.length) {
        // If only single child link, skip recursion and get link url
        if (links.length === 1) {
          normalizedData[id].url = links[0].url;
        } else {
          links.forEach((childNode) => {
            transform({ node: childNode, parentId: id });
          });
        }
      }
    };

    transform({ node: data });
    return normalizedData;
  },
  getNavNodeUrl: () => config.navNodeUrl,
  getLinks: (access = 'public') => config.links.map(link => link[access.toLowerCase()] || link.public),
  getCartCount: () => {
    const cartElement = document.getElementById('mobile-nav');
    return cartElement && cartElement.dataset.carttotal ? cartElement.dataset.carttotal : '';
  }
};

export default util;
