import {
  ORDER_SUMMARY_RECEIVED, TOGGLE_NECTAR_FORM, SHOW_CART_DETAILS,
  HIDE_CART_DETAILS, NECTAR_CARD_APPLIED, INVALID_NECTAR_CARD_ERROR,
  NECTAR_CARD_SUBMITTED, APPLY_NECTAR_CARD, REQUEST_ORDER_SUMMARY
} from '../actions/orderSummaryActions';
import { FAILURE } from '../actions/general';
import { toDomainFulfilment } from './fulfilment';

const defaultState = {
  isLoadingSummary: false,
  products: null,
  previewProducts: null,
  fulfilment: null,
  nectar: null,
  showNectarForm: false,
  isSubmittingNectar: false,
  totalItems: null,
  totalPrice: null,
  subTotal: null,
  discounts: [],
  viewCartDetailsText: null,
  showCartDetails: false
};

const toDomainProduct = (product) => {
  const { price, ...rest } = product;
  const { imageUrl } = rest;

  return {
    ...rest,
    imageUrl: imageUrl && imageUrl !== ''
      ? imageUrl
      : '/_ui/shared/images/bv-missing-product.jpg',
    price: price ? price.value : null,
    wasPrice: price ? price.wasPrice : null,
    wasWasPrice: price ? price.wasWasPrice : null,
    totalPrice: price ? price.totalPrice : null
  };
};

const createCartDetailsText = (products) => {
  if (!products || !products.length) {
    return defaultState.viewCartDetailsText;
  }
  if (products.length === 1) {
    return 'View item details';
  }
  if (products.length === 2 || products.length === 3) {
    return 'View items details';
  }
  return 'View all items';
};

const enableIsLoadingSummary = state => ({ ...state, isLoadingSummary: true });

const orderSummaryReceived = (state, action) => {
  const { orderSummary } = action.payload;
  const {
    products, fulfilmentOption, deliveryAddress, nectar, total, subTotal, discounts
  } = orderSummary;
  const domainProducts = products
    ? products.map(toDomainProduct)
    : null;

  return {
    ...state,
    isLoadingSummary: defaultState.isLoadingSummary,
    products: domainProducts,
    previewProducts: domainProducts ? domainProducts.slice(0, 3) : null,
    fulfilment: toDomainFulfilment(fulfilmentOption, deliveryAddress),
    nectar: nectar || null,
    totalItems: domainProducts ? domainProducts.reduce((acc, { quantity }) => acc + quantity, 0) : null,
    totalPrice: total,
    subTotal,
    discounts,
    viewCartDetailsText: createCartDetailsText(domainProducts)
  };
};

const toggleNectarForm = state => ({ ...state, showNectarForm: !state.showNectarForm });

const activateSubmittingNectar = state => ({ ...state, isSubmittingNectar: true });

const deactivateSubmittingNectar = state => ({ ...state, isSubmittingNectar: false });

const persistNectarCard = (state, action) => {
  const { nectarCardNumber } = action.payload;

  return ({
    ...state,
    nectar: {
      ...state.nectar,
      cardNumber: nectarCardNumber
    }
  });
};

const showCartDetails = state => ({
  ...state,
  showCartDetails: true
});

const hideCartDetails = state => ({
  ...state,
  showCartDetails: false
});

const addInvalidNectarCardError = state => ({
  ...state,
  nectarValidationError: 'Enter your Nectar card number'
});

const failure = (state, action) => {
  switch (action.payload.source) {
    case INVALID_NECTAR_CARD_ERROR:
      return deactivateSubmittingNectar(addInvalidNectarCardError(state));
    case APPLY_NECTAR_CARD:
      return deactivateSubmittingNectar(state);
    default:
      return state;
  }
};

const orderSummaryReducer = (
  state = defaultState,
  action = { type: '', payload: {} }
) => {
  switch (action.type) {
    case REQUEST_ORDER_SUMMARY:
      return enableIsLoadingSummary(state);
    case ORDER_SUMMARY_RECEIVED:
      return orderSummaryReceived(state, action);
    case TOGGLE_NECTAR_FORM:
      return toggleNectarForm(state);
    case NECTAR_CARD_SUBMITTED:
      return activateSubmittingNectar(state);
    case NECTAR_CARD_APPLIED:
      return deactivateSubmittingNectar(persistNectarCard(state, action));
    case SHOW_CART_DETAILS:
      return showCartDetails(state);
    case HIDE_CART_DETAILS:
      return hideCartDetails(state);
    case FAILURE:
      return failure(state, action);
    default:
      return state;
  }
};

export default orderSummaryReducer;
