import { put } from 'redux-saga/effects';
import { nectarCardValidated, nectarCardValidationError } from '../actions/orderSummaryActions';

export const isValidNectarCardNumber = nectarCardNumber => new RegExp('[0-9]{11}').test(nectarCardNumber);

export function* validateNectarCard({ payload: { nectarCardNumber } }) {
  if (isValidNectarCardNumber(nectarCardNumber)) {
    yield put(nectarCardValidated(nectarCardNumber));
  } else {
    yield put(nectarCardValidationError());
  }
}
