const dayToFormattedDay = {
  'Monday': 'Mon',
  'Tuesday': 'Tue',
  'Wednesday': 'Wed',
  'Thursday': 'Thu',
  'Friday': 'Fri',
  'Saturday': 'Sat',
  'Sunday': 'Sun',
};

const formatOpeningTimes = openingTimes => (openingTimes && openingTimes.length > 0
  ? openingTimes
    .map(time => ({
      days: dayToFormattedDay[time.day],
      times: `${time.start_time} - ${time.end_time}`
    }))
  : null);

const convertDate = (dateString) => {
  if (!dateString) {
    return '';
  }

  const date = new Date(dateString);
  if (!(date instanceof Date && !Number.isNaN(date.valueOf()))) {
    return '';
  }

  const day = date.getDate();
  const month = date.getMonth() + 1;
  const year = date.getFullYear().toString().substring(2);
  return `${day}/${month}/${year}`;
};

const formatSpecialOpeningTimes = specialOpeningTimes => (specialOpeningTimes && specialOpeningTimes.length > 0
  ? specialOpeningTimes
    .map(time => ({
      date: convertDate(time.date),
      dayDescription: time.description || '',
      times: time.closed ? 'Closed' : `${time.start_time} - ${time.end_time}`
    }))
  : null);

export { formatOpeningTimes, formatSpecialOpeningTimes };
