import { BAZAAR_VOICE_DATA_RECEIVED } from '../actions/bazaarVoiceActions';

const fitAsExpectedReducer = (state = {
  fitConsensusStats: [],
  fitConsensus: {
    percentage: null,
    description: null
  }
}, { type = '', payload = {} } = {}) => {
  switch (type) {
    case BAZAAR_VOICE_DATA_RECEIVED:
      return { ...state, ...payload };
    default:
      return state;
  }
};

export default fitAsExpectedReducer;
