import {
  SET_MULTI_DELIVERY,
  ITEMS_HAS_ERRORED,
  ITEMS_IS_LOADING,
  SET_DELIVERY_TYPE,
  SET_DELIVERY_DETAIL,
  ITEMS_FETCH_DATA_SUCCESS,
  ADDRESSES_FETCH_DATA_SUCCESS,
  ADD_NEW_ADDRESS_SUCCESS,
  EDIT_ADDRESS_SELECTION,
  SHOW_FORM,
  SHOW_PANEL,
  CLICK_COLLECT_AVAILABLE,
  NEW_ADDRESS_ADDED,
  ADDRESS_EDITED,
  SET_FULFILMENT_OPTIONS,
  RESET_FULFILMENT_INFORMATION,
  SET_DELIVERY_FEE,
  SET_FULFILMENT_CODE,
  SET_FULFILMENT_DATES,
  SET_FULFILMENT_DATE,
  SET_NEAREST_STORES
} from '../constants/type';
import { createFulfilmentInformation } from '../actions/createDeliveryInformation';
import fulfilmentConfig from '../config/fulfilment';

const initialState = {
  deliveryItems: [],
  savedAddresses: [],
  deliveryInformation: [],
  hasErrored: false,
  isLoading: true,
  newAddressAdded: false,
  editedAddress: false,
  multiDelivery: true,
  totalPrice: '0',
  deliveryFee: '0',
  addressEditDetails: {
    details: null,
    editDetails: false
  },
  showPanelSection: false,
  showAddForm: false,
  showEditForm: false,
  clickCollectAvailable: false,
  nearestStores: null
};

const deliveries = (state = initialState, action) => {
  switch (action.type) {
    case SET_MULTI_DELIVERY:
      return {
        ...state,
        multiDelivery: action.payload
      };

    case ITEMS_HAS_ERRORED:
      return {
        ...state,
        hasErrored: action.payload
      };

    case EDIT_ADDRESS_SELECTION:
      return {
        ...state,
        addressEditDetails: { details: action.payload, editDetails: true }
      };

    case SHOW_PANEL:
      return {
        ...state,
        showPanelSection: action.payload
      };

    case CLICK_COLLECT_AVAILABLE:
      return {
        ...state,
        clickCollectAvailable: action.payload
      };

    case ADDRESS_EDITED:
      return {
        ...state,
        editedAddress: action.payload,
        addressEditDetails: { ...state.addressEditDetails, editDetails: action.payload }
      };

    case SHOW_FORM:
      return {
        ...state,
        showEditForm: action.payload.formType === 'editForm',
        showAddForm: action.payload.formType === 'addForm'
      };

    case ITEMS_IS_LOADING:
      return {
        ...state,
        isLoading: action.payload
      };

    case ADD_NEW_ADDRESS_SUCCESS:
      return {
        ...state,
        savedAddresses: action.payload
      };

    case NEW_ADDRESS_ADDED:
      return {
        ...state,
        newAddressAdded: action.payload
      };

    case SET_DELIVERY_TYPE:
      const newInformation = state.deliveryInformation.map(
        (information, index) => (index === action.position ? { ...information, deliveryType: action.deliveryType } : information)
      );

      return { ...state, deliveryInformation: newInformation };

    case SET_DELIVERY_DETAIL:
      const newDeliveryInformation = state.deliveryInformation.map((information, index) => {
        if (index === action.position) {
          const newDeliveryAddress = { ...information.deliveryAddress, [action.option]: action.updatedData };
          return { ...information, deliveryAddress: newDeliveryAddress };
        }
        return information;
      });
      return { ...state, deliveryInformation: newDeliveryInformation };

    case ITEMS_FETCH_DATA_SUCCESS:
      return {
        ...state,
        deliveryItems: action.payload.deliveryItems,
        totalPrice: action.payload.totalPrice,
        deliveryInformation: action.payload.deliveryInformation
      };

    case ADDRESSES_FETCH_DATA_SUCCESS:
      return {
        ...state,
        savedAddresses: action.payload.savedAddresses
      };

    case SET_FULFILMENT_OPTIONS:
      return {
        ...state,
        deliveryInformation: state.deliveryInformation.map((information, index) => {
          if (index === action.payload.position) {
            return {
              ...information,
              fulfilmentInformation: {
                ...information.fulfilmentInformation,
                options: action.payload.options
              }
            };
          }
          return information;
        })
      };

    case RESET_FULFILMENT_INFORMATION:
      return {
        ...state,
        deliveryInformation: state.deliveryInformation.map((information, index) => {
          if (index === action.payload.position) {
            return {
              ...information,
              fulfilmentInformation: createFulfilmentInformation()
            };
          }
          return information;
        })
      };

    case SET_FULFILMENT_CODE:
      return {
        ...state,
        deliveryInformation: state.deliveryInformation.map((information, index) => {
          if (index === action.payload.position) {
            return {
              ...information,
              fulfilmentInformation: {
                ...information.fulfilmentInformation,
                code: action.payload.code || createFulfilmentInformation().code
              }
            };
          }
          return information;
        })
      };

    case SET_FULFILMENT_DATES:
      return {
        ...state,
        deliveryInformation: state.deliveryInformation.map((information, index) => {
          if (index === action.payload.position) {
            return {
              ...information,
              fulfilmentInformation: {
                ...information.fulfilmentInformation,
                dates: action.payload.dates
              }
            };
          }
          return information;
        })
      };

    case SET_FULFILMENT_DATE:
      return {
        ...state,
        deliveryInformation: state.deliveryInformation.map((information, index) => {
          if (index === action.payload.position) {
            return {
              ...information,
              fulfilmentInformation: {
                ...information.fulfilmentInformation,
                date: action.payload.date || createFulfilmentInformation().date
              }
            };
          }
          return information;
        })
      };

    case SET_DELIVERY_FEE:
      return {
        ...state,
        deliveryFee: action.payload || initialState.deliveryFee
      };

    case SET_NEAREST_STORES:
      const nearestStores = {};
      action.payload.forEach((store) => {
        nearestStores[store.id] = { ...store, showMapPin: true };
      });
      return {
        ...state,
        nearestStores
      };

    default:
      return state;
  }
};

export default deliveries;

// Selectors
export const getDeliveryInformation = (state, position) => state.deliveryInformation[position] || {};
export const getSavedAddresses = state => state.savedAddresses;
export const getSelectedSavedAddressId = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  const { deliveryAddress } = deliveryInformation;
  const { id } = deliveryAddress || {};
  return id || '';
};
export const getPostCodeOfSavedAddress = (state, savedAddressId) => {
  const savedAddress = getSavedAddresses(state).find(address => address.id === savedAddressId) || {};
  return savedAddress.postcode || '';
};
export const getErrorStatus = state => state.hasErrored;
export const getLoadingStatus = state => state.isLoading;
export const getDeliveryItems = state => state.deliveryItems;
export const getAddressEditDetails = state => state.addressEditDetails;
export const getNewAddressAddedStatus = state => state.newAddressAdded;
export const getEditedAddressStatus = state => state.editedAddress;
export const getShowPanelSectionStatus = state => state.showPanelSection;
export const getClickCollectAvailableStatus = state => state.clickCollectAvailable;
export const getTotalPrice = state => state.totalPrice;
export const getDeliveryFee = state => state.deliveryFee;
export const getMultiDeliveryStatus = state => state.multiDelivery;
export const getShowEditFormStatus = state => state.showEditForm;
export const getShowAddFormStatus = state => state.showAddForm;
export const getDeliveryType = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  return deliveryInformation.deliveryType || '';
};
export const getFulfilmentCode = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  return deliveryInformation.fulfilmentInformation.code;
};
export const getFulfilmentOptions = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  const { options } = deliveryInformation.fulfilmentInformation;
  return options.map(option => (
    { ...option, isPremium: option.code === fulfilmentConfig.premiumDelivery }
  ));
};
export const getFulfilmentDate = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  return deliveryInformation.fulfilmentInformation.date;
};
export const getFulfilmentDates = (state, position) => {
  const deliveryInformation = getDeliveryInformation(state, position);
  const { dates } = deliveryInformation.fulfilmentInformation;
  return fulfilmentConfig.getParsedDates(dates);
};
export const getNearestStores = state => (
  state.nearestStores ? Object.values(state.nearestStores) : []
);
