import { call, put } from 'redux-saga/effects';
import get from '../helpers/axiosGetHelper';
import config from '../config/storeLocation';

import {
  storesListReceived,
  storesListFailed
} from '../actions/storeLocatorActions';

export function* fetchStoresList({ payload: { postcodeOrTown } }) {
  try {
    const {
      data: { stores }
    } = yield call(get, `${config.apiUrl}?location=${postcodeOrTown}`);
    yield put(storesListReceived(stores));
  } catch (error) {
    if (error.response && error.response.status === 404) {
      yield put(storesListReceived([]));
    } else {
      yield put(storesListFailed(error));
    }
  }
}
