import React, { Component, Suspense } from 'react';
import PropTypes from 'prop-types';
import Overlay from '../Overlay/Overlay';
import BurgerIcon from './presentational/BurgerIcon';
import SiteLogo from './presentational/SiteLogo';
import NavItems from './presentational/NavItems';
import MobileNavWrapper from './presentational/MobileNavWrapper';
import ErrorBoundary from '../../presentational/ErrorBoundary';
import fetch from '../../helpers/axiosGetHelper';
import util from './config/util';

const SearchBox = React.lazy(() => import(/* webpackChunkName: 'SearchBox' */ './presentational/SearchBox'));

class MobileNavContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      data: null,
      showSearch: false,
      cartCount: util.getCartCount()
    };
  }

  async componentDidMount() {
    try {
      const response = await fetch(util.getNavNodeUrl());
      if (response && response.data) {
        const { access } = this.props;
        this.setState({
          data: {
            navNodes: util.normalize(response.data),
            links: util.getLinks(access)
          }
        });
      }
    } catch (err) {
      // eslint-disable-next-line no-console
      console.warn('Error fetching navigation nodes', err);
    }
  }

  toggleMobileNav = () => {
    const { data } = this.state;
    if (data) {
      this.setState(prevState => ({
        active: !prevState.active
      }));
    }
  }

  toggleSearchNav = (searchstate = false) => {
    this.setState({
      showSearch: searchstate
    });
  }

  updateItemCount = (count) => {
    this.setState({
      cartCount: count
    });
  }

  render() {
    const {
      active, data, showSearch, cartCount
    } = this.state;
    return (
      <React.Fragment>
        <ErrorBoundary>
          <Overlay active={active} onOverlayClick={this.toggleMobileNav} />
        </ErrorBoundary>
        {data && (
        <ErrorBoundary>
          <MobileNavWrapper active={active} data={data} />
        </ErrorBoundary>
        )}
        <nav
          role='navigation'
          aria-label='Primary navigation'
          className='ln-u-display-flex ln-u-justify-content-space-between tu-u-background--white ln-u-border-bottom tu-c-mobile-header__container'
        >
          <div className='ln-u-display-flex ln-u-align-items-center'>
            <ErrorBoundary>
              <BurgerIcon active={active} onIconClick={this.toggleMobileNav} />
            </ErrorBoundary>
            <ErrorBoundary>
              <SiteLogo />
            </ErrorBoundary>
          </div>
          <ErrorBoundary>
            <NavItems
              cartCount={cartCount}
              searchStatus={showSearch}
              onIconClick={(searchstate) => {
                this.toggleSearchNav(searchstate);
              }}
            />
          </ErrorBoundary>
        </nav>
        { showSearch && (
        <Suspense fallback={<p className='ln-u-h3 ln-u-soft ln-u-display-block ln-u-flush-bottom'>Loading...</p>}>
          <ErrorBoundary>
            <SearchBox />
          </ErrorBoundary>
        </Suspense>
        )}
      </React.Fragment>
    );
  }
}

MobileNavContainer.propTypes = {
  access: PropTypes.string.isRequired
};

export default MobileNavContainer;
