import React from 'react';
import ReactDOM from 'react-dom';

const mountCheckoutOrderSummary = (store, document, onOverlayActive, onOverlayInactive) => {
  const mountPoint = document.getElementById('checkout-order-summary');

  if (mountPoint) {
    return import(/* webpackChunkName: "CheckoutOrderSummary" */ './CheckoutOrderSummary.provider')
      .then(({ default: ProvidedCheckoutOrderSummary }) => {
        ReactDOM.render(
          <ProvidedCheckoutOrderSummary
            store={store}
            onOverlayActive={onOverlayActive}
            onOverlayInactive={onOverlayInactive}
          />,
          mountPoint
        );
      });
  }

  return null;
};

export default mountCheckoutOrderSummary;
