import globalSettingsReducer from './globalSettingsReducer';
import fitAsExpectedReducer from './fitAsExpectedReducer';
import storeLocatorReducer from './storeLocatorReducer';
import orderSummaryReducer from './orderSummaryReducer';
import productMediaReducer from './productMediaReducer';

const createRootReducer = combineReducers => combineReducers({
  globalSettings: globalSettingsReducer,
  fitAsExpected: fitAsExpectedReducer,
  storeLocation: storeLocatorReducer,
  orderSummary: orderSummaryReducer,
  productImages: productMediaReducer
});

export default createRootReducer;
