import React from 'react';
import ReactDOM from 'react-dom';

const mountRecommendations = (document) => {
  const mountPoint = document.getElementById('recommendationsWrapper');

  if (mountPoint) {
    const { sku: productCode } = mountPoint.dataset;

    return import(/* webpackChunkName: "Recommendations" */ '../../components/Recommendations/Recommendations.container')
      .then(({ default: Recommendations }) => {
        ReactDOM.render(<Recommendations productCode={productCode} />, mountPoint);
      });
  }

  return null;
};

export default mountRecommendations;
