export const getSizes = (sizeListString) => {
  const isEmptyString = string => string;

  return sizeListString
    .split(';')
    .filter(isEmptyString)
    .reduce((acc, sizeEntry) => {
      const [code, size, price = '', stock = ''] = sizeEntry.split('=');
      return acc.concat({
        value: code,
        text: size,
        price,
        stock,
        selectable: !((stock.includes('Out of Stock') || (stock.includes('Back in stock soon'))))
      });
    }, []);
};

export const shouldRenderTiles = (sizes, numOfSizes, charCount) => {
  if (sizes.length > numOfSizes) {
    return false;
  }
  if (sizes.some(({ text }) => text.length > charCount)) {
    return false;
  }
  return true;
};

export const getNumOfSizes = str => Number(str) || 20;
export const getCharCount = str => Number(str) || 4;

export const buildProps = (sizeString, numOfSizes, charCount) => {
  const [sizeListString, preselectedSize = ''] = sizeString.split('!');
  const sizes = getSizes(sizeListString);
  return {
    preselectedSize,
    sizes,
    shouldRenderTiles: shouldRenderTiles(sizes, getNumOfSizes(numOfSizes), getCharCount(charCount))
  };
};
