import React from 'react';
import PropTypes from 'prop-types';
import Icon from '../../../presentational/Icon';

const NavItems = ({ cartCount, onIconClick, searchStatus }) => (
  <div className='ln-u-display-flex ln-u-align-items-stretch'>
    <a href='/store-finder' title='Store locator' className='ln-u-border-right ln-u-soft ln-u-display-flex ln-u-align-items-center ln-u-justify-content-center tu-c-mobile-header__nav-items'>
      <Icon classes='ln-c-icon ln-c-icon--cart' icon='location' viewBox='0 0 18 24' strokeWidth='2' fill='none' />
    </a>
    <button
      type='button'
      title='Search'
      className={`ln-u-soft ln-u-display-flex ln-u-align-items-center ln-u-justify-content-center tu-c-mobile-header__nav-items${searchStatus ? ' tu-c-mobile-header__nav-items--active' : ''}`}
      onClick={() => {
        onIconClick(!searchStatus);
      }
        }
    >
      <Icon classes='ln-c-icon ln-c-icon--cart' icon='searchglass' strokeWidth='0' viewBox='0 0 22 22' fill={searchStatus ? '#fff' : '#000'} />
    </button>
    <a href='/cart' title='Cart' className='ln-u-border-left ln-u-soft ln-u-display-flex  ln-u-justify-content-center tu-c-mobile-header__nav-items ln-u-align-items-center tu-b-position'>
      <Icon classes='ln-c-icon ln-c-icon--cart' icon='basket' viewBox='0 0 42 44' strokeWidth='0' fill='#000' />
      <span className='tu-c-mobile-header__cart-count'>{cartCount}</span>
    </a>
  </div>
);

NavItems.propTypes = {
  cartCount: PropTypes.string.isRequired,
  onIconClick: PropTypes.func.isRequired,
  searchStatus: PropTypes.bool.isRequired,
};

export default NavItems;
