import LazyLoad from 'vanilla-lazyload/dist/lazyload.min';
import lazyloadConfig from '../config/lazyload';

const itemSwapService = (function () {
  return {
    productsContainer: document.getElementsByClassName('js-product-items')[0],
    toggleSwitch: document.getElementsByClassName('js-toggleSwitch')[0],
    localStorageItem: 'modelFlat',
    flatClass: 'tu-c-product-container--flat',
    modelFlatStatus: false,
    lazyload: null,
    checkImageStatus: function () {
      const status = JSON.parse(localStorage.getItem(this.localStorageItem));
      if (status) {
        this.modelFlatStatus = true;
        this.toggleSwitch.checked = true;
      }
      return this.modelFlatStatus;
    },
    setImageStatus: function (status) {
      if (this.productsContainer === undefined) {
        return
      };

      if (status) {
        this.productsContainer.classList.add(this.flatClass);
      } else {
        this.productsContainer.classList.remove(this.flatClass);
      }
      return true
    },
    lazyLoad: function () {
      this.lazyload = new LazyLoad(lazyloadConfig);
    },
    updatelazyLoad: function () {
      if (this.lazyload) {
        this.lazyload.update();
      }
    },
    setLocalStorage: function (status) {
      localStorage.setItem(itemSwapService.localStorageItem, status);
    },
    init: function () {
      if (this.toggleSwitch === undefined) {
        return
      };

      const setLazyLoad = new Promise((resolve, reject) => {
        const setImage = itemSwapService.setImageStatus(itemSwapService.checkImageStatus());
        resolve(setImage);
      });

      setLazyLoad.then((setImage) => {
        itemSwapService.lazyLoad();
      });

      this.toggleSwitch.addEventListener('change', (e) => {
        itemSwapService.setLocalStorage(e.target.checked);
        itemSwapService.modelFlatStatus = e.target.checked;
        itemSwapService.setImageStatus(itemSwapService.modelFlatStatus);
      });
    }
  };
}());

export default itemSwapService;