import { put } from 'redux-saga/effects';

import getHelper from '../helpers/axiosGetHelper';
import { productApi } from './helpers/endpointDetails';
import { secondaryProductMediaReceived, secondaryProductMediaUnavailable } from '../actions/productMediaActions';
import { newRelicPageActionSubmitted } from '../actions/general';

export function* fetchSecondaryProductMediaData({ payload: { productCode, productName } }) {
  try {
    const url = productApi().endpoints.url(productCode);
    const response = yield getHelper(url);
    const { data } = response;
    const { imageResource = {} } = data;
    const { cdnImages = [] } = imageResource;

    if (cdnImages.length > 0) {
      yield put(secondaryProductMediaReceived({ productCode, productName, ...data }));
    } else {
      yield put(secondaryProductMediaUnavailable(productCode, productName));
      yield put(newRelicPageActionSubmitted('product-fallback-image-used', response));
    }
  } catch (error) {
    yield put(secondaryProductMediaUnavailable(productCode, productName));
    yield put(newRelicPageActionSubmitted('product-fallback-image-used', error.toJSON()));
  }
}
