import { call, put } from 'redux-saga/effects';
import axios from 'axios';
import { postcodeOrTownReceived, requestNearestPostalCodeFailed } from '../actions/storeLocatorActions';
import config from '../config/storeLocation';

export const hasOnlyPostalCodeType = (address) => {
  const { types } = address || {};
  return (Array.isArray(types) && types.length === 1 && types[0] === 'postal_code') || false;
};

export const findAddressWithPostalCodeType = addressList => (Array.isArray(addressList) && addressList.find(hasOnlyPostalCodeType)) || null;

export const getPostalCode = ({ results }) => {
  const resultWithPostalCode = findAddressWithPostalCodeType(results);
  const { address_components: addressComponents } = resultWithPostalCode || {};
  const addressWithPostalCode = findAddressWithPostalCodeType(addressComponents);
  const { long_name: postalCode } = addressWithPostalCode || {};
  return postalCode || '';
};

export function* fetchNearestPostalCode({ payload: { apiKey, coords } }) {
  const url = `${config.reverseGeocodingUrl}&key=${apiKey}&latlng=${coords}`;
  try {
    const { data } = yield call(axios.get, url);
    const postalCode = getPostalCode(data);
    if (postalCode) {
      yield put(postcodeOrTownReceived(postalCode));
    } else {
      yield put(requestNearestPostalCodeFailed());
    }
  } catch (error) {
    yield put(requestNearestPostalCodeFailed(error));
  }
}
