import React from 'react';
import PropTypes from 'prop-types';

const LevelOneItem = ({ item, onItemClick }) => {
  const {
    id, text, fontColor, url, media, childIds
  } = item;

  return text
    ? (
      <a
        href={url}
        title={text}
        onClick={(e) => {
          if (childIds.length) {
            e.preventDefault();
            onItemClick(id);
          }
        }}
        className='ln-u-display-block ln-u-text-decoration-normal'
      >
        <div
          className='ln-u-display-flex ln-u-align-items-center ln-u-push-top
            tu-c-mobile-nav__list-container--level-one-item-image'
          style={{ color: `${fontColor}`, backgroundImage: `url(${media && media.url ? media.url : ''})` }}
        >
          <div className='ln-u-soft-left ln-u-font-weight-medium'>{text}</div>
        </div>
      </a>
    )
    : null;
};

LevelOneItem.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number,
    parentId: PropTypes.number,
    type: PropTypes.oneOf(['navigation']),
    text: PropTypes.string,
    fontColor: PropTypes.string,
    url: PropTypes.string,
    media: PropTypes.shape({
      url: PropTypes.string
    })
  }).isRequired,
  onItemClick: PropTypes.func.isRequired
};

export default LevelOneItem;
