const lang = 'en-gb';
const currency = 'GBP';

const extractItems = (data) => {
  const entries = data.entries || [];
  const items = [];
  entries.forEach((entry) => {
    const refCode = (entry.product && entry.product.baseProduct) || null;
    const qty = entry.quantity || null;
    const price = (entry.basePrice && entry.basePrice.value) || null;
    const item = { refCode, qty, price };

    items.push(item);
  });

  return items;
};

const extractBasketPayload = (data) => {
  const items = extractItems(data);

  return {
    type: 'basket',
    lang,
    basket: {
      items,
      currency
    }
  };
};

const extractConfirmationPayload = (data) => {
  const items = extractItems(data);

  const orderNo = (data && data.code) || null;
  const subtotal = (data && data.subTotalWithDiscounts && data.subTotalWithDiscounts.value) || null;
  const shipping = (data && data.deliveryMode && data.deliveryMode.deliveryCost
    && data.deliveryMode.deliveryCost.value) || null;
  const total = (data && data.totalPrice && data.totalPrice.value) || null;

  return {
    type: 'order',
    lang,
    order: {
      orderNo,
      items,
      currency,
      subtotal,
      shipping,
      total
    }
  };
};

const extractProductPayload = (data) => {
  /*
    In order to reliably identify the colour variant SKU (as required by Episerver tracking) in the data
    returned from the product endpoint, whether we invoke it with a StyleVariant SKU or a SizeVariant SKU,
    we need to traverse the document using the following:

    1. within baseOptions array, find the object that has variantType of 'ApparelColourVariantProduct'.
       There should always be exactly ONE of these.
    2. within this option object, find the selected object. There should always be exactly ONE of these.
    3. within this selected object find the code property (string). There should always be exactly ONE of these.

    e.g. baseOptions[where option.variantType === 'ApparelColourVariantProduct'].selected.code

    The following extraction code is defensive of missing elements within this chain and will return null
    if the chain breaks down due to missing data.
   */
  const baseOptions = (data && data.baseOptions) || [];
  const colourOptions = baseOptions.filter(option => option.variantType === 'ApparelColourVariantProduct');
  const colourOption = (colourOptions && colourOptions[0]) || {};
  const selected = colourOption.selected || {};
  const refCode = selected.code || null;

  return {
    type: 'product',
    lang,
    smartRecs: {
      markRecsAsShown: false,
      showAttributes: ['*']
    },
    recContent: 'full',
    product: {
      refCode
    },
    channel: 'web',
  };
};

const loadPeeriusTracker = (trackingUrl) => {
  const peeriusScript = document.createElement('script');
  peeriusScript.type = 'text/JavaScript';
  peeriusScript.src = trackingUrl;
  peeriusScript.charset = 'UTF-8';
  peeriusScript.defer = true;

  document.body.appendChild(peeriusScript);
};

const peerius = {
  trigger: ({ type, data, trackingUrl } = {}) => {
    let payload;

    if (!trackingUrl) {
      return;
    }

    switch (type) {
      case 'cart':
        payload = extractBasketPayload(data);
        break;

      case 'confirmation':
        payload = extractConfirmationPayload(data);
        break;

      case 'pdp':
        payload = extractProductPayload(data);
        break;

      default:
        // unrecognised type
        console.warn(`Peerius Metrics configuration mismatch: type[${type}]`);
    }

    if (payload) {
      window.PeeriusCallbacks = {
        ...window.PeeriusCallbacks,
        track: payload
      };

      loadPeeriusTracker(trackingUrl);
    }
  }
};

export default peerius;
