const hallettCartService = (function () {
  return {
    $cartItems: document.getElementsByClassName('js-show-cart-items')[0],
    $showCart: document.getElementsByClassName('js-show-cart'),
    $hideClass: 'ln-u-hidden',
    $cartStatus: true,
    changeClass: function (status = false) {
      if (!this.$cartItems) {
        return false
      }

      if (status) {
        this.$cartItems.classList.add(this.$hideClass);
      } else {
        this.$cartItems.classList.remove(this.$hideClass);
      }

      return true
    },
    changeCartStatus: function () {
      this.$cartStatus = !this.$cartStatus;
    },
    init: function () {
      for (let i = 0; i < this.$showCart.length; i++) {
        this.$showCart[i].addEventListener('click', (e) => {
          e.preventDefault();
          hallettCartService.changeCartStatus();
          hallettCartService.changeClass(hallettCartService.$cartStatus);
        });
      };
    }
  };
}());

export default hallettCartService;
