import React from 'react';
import ReactDOM from 'react-dom';

const findLastProductCode = skuNodes => skuNodes[skuNodes.length - 1].dataset.sku;

const getAndParseProductCode = (document) => {
  const fitAsExpectedData = document.querySelectorAll('.js-fit-as-expected[data-sku]');
  if (!fitAsExpectedData.length) return null;
  const sku = findLastProductCode(fitAsExpectedData);
  return sku.split('-')[0];
};

const mountFitAsExpected = (store, document) => {
  const fitAsExpected = document.getElementById('js-fit-as-expected');

  if (fitAsExpected) {
    const productCode = getAndParseProductCode(document);

    return import(/* webpackChunkName: "FitAsExpected" */ './ProvidedFitAsExpected')
      .then(({ default: ProvidedFitAsExpected }) => {
        ReactDOM.render(<ProvidedFitAsExpected productCode={productCode} store={store} />, fitAsExpected);
      });
  }

  return null;
};

export default mountFitAsExpected;
