const bazaarVoiceReviewsUrl = 'https://api.bazaarvoice.com/data/reviews.json';

const buildBazaarVoiceReviewsParameters = (apiKey, productCode) => [
  'apiversion=5.4',
  `passkey=${apiKey}`,
  'locale=en_GB',
  `Filter=ProductId:${productCode}`,
  'Filter=IsRatingsOnly:false',
  'Include=Products',
  'Stats=Reviews',
  'Limit=100',
  'Offset=0',
  'Sort=SubmissionTime:Desc'
].join('&');

