import React from 'react';
import ReactDOM from 'react-dom';

const mountCPMRegistration = (document) => {
  const mountPoint = document.getElementById('CPM-registration');

  if (mountPoint) {
    return import(/* webpackChunkName: "CPMRegistration" */ '../../components/CPM/CPMRegistration')
      .then(({ default: CPMRegistration }) => {
        ReactDOM.render(<CPMRegistration />, mountPoint);
      });
  }

  return null;
};

export default mountCPMRegistration;
